import React, { useState, useEffect, useRef } from 'react';
import { SchedulerItem, SchedulerSlot } from '@progress/kendo-react-scheduler';
import '@progress/kendo-theme-default/dist/all.css';
import { guid } from '@progress/kendo-react-common';
import { useNavigate } from 'react-router-dom';
import DateService from '../../../core/date.service';
import ScheduleService from '../ScheduleService';
import styles from './ReactSchedule.module.css';
import './kendo-scheduler.css';
import GeneratedScheduleBannerComponent from '../generated-schedule/generated-schedule-banner/GeneratedScheduleBannerComponent';
import AuthService from '../../../core/auth.service';
import useTableSize from '../../../shared/shared';
import { AddProgramsComponent } from './forms/AddProgramsComponent';
import { EditProgramsComponent } from './forms/EditProgramsComponent';
import {
  PasteComponent,
  DeleteComponent,
  ConfirmSubmitModalComponent,
  SlotDropdownComponent,
} from './forms/SchedulerFormsComponent';
import {
  success,
  danger,
} from '../../../shared/elements/notification/NotifyComponent';
import DefaultTimeComponent from './activehours/DefaultTimeComponent';
import StandardTimeComponent from './activehours/StandardTimeComponent';
import moment from 'moment';
import {
  uploadFileIcon,
  downloadFileIcon,
  broadcastTimeIcon,
  standardTimeIcon,
} from '../../../assets';
import leftIcon from '../../../assets/icons/chev-left.svg';
import rightIcon from '../../../assets/icons/chev-right.svg';
import generatedIcon from '../../../assets/icons/Generated-Duration.svg';
import originalIcon from '../../../assets/icons/Original-Duration.svg';
import pin12Icon from '../../../assets/icons/pin12.png';
import pin15Icon from '../../../assets/icons/pin15.png';
import sponsorshipIcon from '../../../assets/icons/sponsorship.png';
import freePlayIcon from '../../../assets/icons/freeplay.png';
import { useCallbackPrompt } from '../../_reusable/blocker/UsePromptComponent.ts';
import ConfirmLeaveModal from '../../../shared/components/confirm-leave-modal/ConfirmLeaveModal';
import { ImportSlotsComponent } from './forms/import/ImportSlotsComponent';
import SchedulerStorage from './SchedulerStorage';
import { padStart } from '@fullcalendar/react';
import ExportModalComponent from './forms/ExportModalComponent';
import SaveButtonComponent from './SaveButtonComponent';

function SchedulerComponent() {
  const saveButtonRef = useRef();
  const navigate = useNavigate();
  const { formTypes } = SchedulerStorage;
  const [isDirty, setIsDirty] = useState(false);
  const [showPrompt, confirmNavigation, cancelNavigation] = useCallbackPrompt(
    isDirty
  );
  const [isLoading, setIsLoading] = useState(true);
  const [scheduleInfo, setSCheduleInfo] = useState({
    channelID: 0,
    channelName: '',
    month: '',
    year: '',
  });
  const [filterSchedule, setFilterSchedule] = useState({});
  const [scheduleID, setScheduleID] = useState(1);
  const [disabledSubmit, setDisabledSubmit] = useState(true);
  const [disabledStatistics, setDisabledStatistics] = useState(true);
  const [disabledExport, setDisabledExport] = useState(true);
  const [isExporting, setIsExporting] = useState(false);
  const [scheduleScores, setScheduleScores] = useState('');
  const [tableWidth, tableHeight] = useTableSize();
  const [isAddProgram, setIsAddProgram] = useState(false);
  const [openForm, setOpenForm] = useState('');
  const [selectedSlot, setSelectedSlot] = useState({});
  const [cutSlot, setCutSlot] = useState({});
  const [duplicateActive, setDuplicateActive] = useState(false);
  const [editTitle, setEditTitle] = useState(null);
  const clockStart = -6;
  const [isStandard, setIsStandard] = useState(true);
  const [history, setHistory] = useState([]);
  const [sampleData, setSampleData] = useState([]);
  const [currentYear, setCurrentYear] = useState(null);
  const [dateRange, setDateRange] = useState({
    start: new Date(),
    end: new Date(),
  });
  const [displayDate, setDisplayDate] = useState(
    new Date(Date.UTC(currentYear, 10, 1))
  );
  const [defaultAddDate, setDefaultAddDDate] = useState(
    new Date(Date.UTC(currentYear, 10, 1))
  );
  const [disablePrevious, setDisablePrevious] = useState(true);
  const [disableNext, setDisableNext] = useState(false);
  const [isGeneratedDuration, setIsGeneratedDuration] = useState(true);
  const [isScheduleCreated, setIsScheduleCreated] = useState(false);
  const [period, setPeriod] = useState({});
  const [generatedSchedule, setGeneratedSchedule] = useState(false);
  const [organisation, setOrganisation] = useState('');
  const [showExportModal, setShowExportModal] = useState(false);
  const [predefinedScheduleData, setPredefinedScheduleData] = useState(null);
  const [op2miseLoading, setOp2miseLoading] = useState(false);
  const isEmptyOrSpaces = (str) => {
    if (str === undefined) {
      return true;
    }
    return str === null || str.match(/^ *$/) !== null;
  };

  const parseAdjust = (eventDate) => {
    let date = new Date(eventDate);
    let totalMinutes = date.getMinutes();
    let number = totalMinutes % 10;
    let minutesToAdd = totalMinutes;
    if (number !== 5 && number !== 0) {
      if (number < 5) {
        if (number === 2 || number === 1) minutesToAdd = totalMinutes - number;
        else minutesToAdd = totalMinutes - number + 5;
      } else {
        if (number === 6 || number === 7)
          minutesToAdd = totalMinutes - number + 5;
        else minutesToAdd = totalMinutes - number + 10;
      }
    }
    minutesToAdd = minutesToAdd - totalMinutes;
    date = new Date(DateService.addMinutes(date, minutesToAdd));
    return date;
  };
  const randomInt = (min, max) =>
    Math.floor(Math.random() * (max - min + 1)) + min;

  const getGeneratedScheduleDetails = (data, filter) => {
    ScheduleService.getCalendarDetails(
      data.id,
      filter.channelId,
      filter.month,
      filter.year,
      clockStart
    )
      .then((response) => {
        if (response == false) {
          navigate(`${process.env.REACT_APP_SUBFOLDER}/schedule`);
        } else {
          if (data.id > 0) {
            setIsStandard(response.data.standardTime);
            const responseData = response.data.scheduleDetails.map(
              (dataItem, index) => ({
                id: index,
                start: parseAdjust(
                  DateService.addHours(dataItem.startAdjusted, clockStart)
                ),
                end: parseAdjust(
                  DateService.addHours(dataItem.endAdjusted, clockStart)
                ),
                startDate: dataItem.start,
                endDate: dataItem.end,
                title: dataItem.title,
                description: 'description',
                isAllDay: false,
                extendedProps: dataItem.extendedProps,
                duration: dataItem.duration,
                durationCurrent: dataItem.duration,
                fixed: dataItem.fixed,
              })
            );
            setSampleData(responseData);
            setScheduleScores(response.data.scoreLabel);
          }
          setOrganisation(response.data.organisationName);
          setIsScheduleCreated(response.data.isCreated);
          setSCheduleInfo({
            channelID: response.data.channelID,
            channelName: response.data.channelName,
            period: data.period,
          });
          setGeneratedSchedule(response.data.isCreated === null);
          setDisabledSubmit(
            response.data.disableSubmission || response.data.isCreated
          );
          setDisabledExport(response.data.disableExport);
          setDisabledStatistics(response.data.disableStatistics);
          setTimeout(() => {
            setIsLoading(false);
          }, 5000);
        }
      })
      .catch((error) => {
        const responseMessage = error.response.data.message;
      });
  };

  const submitSchedule = () => {
    const submission = {
      channelID: scheduleInfo.channelID,
      scheduleID,
      Start: scheduleInfo.period.split('-')[0],
      End: scheduleInfo.period.split('-')[1],
    };
    setDisabledSubmit(true);
    ScheduleService.submitSchedule(submission).then(() => {
      setOpenForm(formTypes.submit);
    });
  };

  const callbackSchedule = () => {
    const scheduledata = filterSchedule;
    navigate(`${process.env.REACT_APP_SUBFOLDER}/schedule`, {
      state: { scheduledata, scheduleInfo, period },
    });
  };

  const handleExport = (id) => {
    if (!isExporting) {
      const queryString = `?ScheduleId=${id}&ChannelId=${scheduleInfo.channelID}&Standard=${isStandard}`;
      setIsExporting(true);
      ScheduleService.exportSubmissions(queryString)
        .then((response) => {
          const blob = new Blob([response.data], {
            type:
              'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
          });
          let periodSplit = scheduleInfo.period.split('-');
          let startSplit = periodSplit[0].split('/');
          let endSplit = periodSplit[1].split('/');

          const startString = startSplit[0] + startSplit[1] + startSplit[2];
          const endString = endSplit[0] + endSplit[1] + endSplit[2];
          let exportDate = new Date();
          const exportedDateString =
            padStart(exportDate.getDate(), 2).toString() +
            padStart(exportDate.getMonth() + 1, 2).toString() +
            exportDate.getFullYear().toString();

          saveAs(
            blob,
            `${organisation}_op2mise_schedule_export_${scheduleInfo.channelName}_${startString}_${endString}_${exportedDateString}.xlsx`
          );
        })
        .catch(() => danger('Cannot export a schedule with no saved slots'))
        .finally(() => {
          setIsExporting(false);
          setShowExportModal(false);
        });
    }
  };

  const checkDirtyExport = () => {
    if (isDirty) setShowExportModal(true);
    else handleExport(scheduleID);
  };

  const handleExportStatistics = () => {
    if (!isExporting) {
      const queryString = `?ScheduleId=${scheduleID}&ChannelId=${scheduleInfo.channelID}`;
      setIsExporting(true);
      ScheduleService.exportStatistics(queryString)
        .then((response) => {
          const blob = new Blob([response.data], {
            type:
              'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
          });
          saveAs(
            blob,
            `${organisation}_op2mise_generated_statistics_export_${
              scheduleInfo.channelName
            }_${scheduleInfo.month}_${
              scheduleInfo.year
            }_${DateService.toExportFormat(new Date())}.zip`
          );
        })
        .finally(() => {
          setIsExporting(false);
        });
    }
  };

  const handleDuplicate = (selected) => {
    let data = selected.dataItem;
    data.id = guid();
    data.start = selected.start;
    const items = [...sampleData];
    const duplicates = items.filter(
      (item) => selected.start.getTime() === item.start.getTime()
    );
    if (duplicates.length < 2) {
      setHistory((old) => old.concat({ data: sampleData }));
      setSampleData((old) => old.concat(data));
      setIsDirty(true);
      setSelectedSlot(selected);
      setEditTitle(selected.dataItem.title);
      setOpenForm(formTypes.edit);
    } else {
      danger('Maximum duplicate reached');
    }
  };

  const sampleDiv = (props) => {
    const { title } = props.dataItem;
    const grade = title.grade;
    const certificate = title.certificate;
    const pin = title.pin == null ? '' : title.pin;
    let timeStamp = DateService.toTimeFormat(props.dataItem.startDate);
    if (parseInt(timeStamp.split(':')[0]) < 6 && !isStandard) {
      const hh = parseInt(timeStamp.split(':')[0]) + 24;
      timeStamp = hh + ':' + timeStamp.split(':')[1];
    }
    const genre = '';
    let gradeCertificateString = grade;

    if (isEmptyOrSpaces(gradeCertificateString)) {
      gradeCertificateString = certificate;
    } else if (!isEmptyOrSpaces(certificate)) {
      gradeCertificateString = `${gradeCertificateString}, ${certificate}`;
    }

    if (isEmptyOrSpaces(gradeCertificateString)) gradeCertificateString = pin;
    else if (!isEmptyOrSpaces(pin))
      gradeCertificateString = `${gradeCertificateString}, ${pin}`;

    const durationInt =
      (props.dataItem.end.getTime() - props.dataItem.start.getTime()) /
      1000 /
      60 /
      60;
    const durationSplit = props.dataItem.durationCurrent.split(':');
    const duration =
      parseInt(durationSplit[0]) * 60 + parseInt(durationSplit[1]);
    return (
      <div
        title={`${timeStamp} (${duration} mins) ${genre}\n${title.titleName}\n${
          grade ? grade : ''
        }\n${certificate ? certificate : ''}`}
        className={styles.divParent}
      >
        <SlotDropdownComponent
          slot={props}
          handleDuplicate={handleDuplicate}
          setSelectedSlot={setSelectedSlot}
          setOpenForm={setOpenForm}
          setEditTitle={setEditTitle}
          handleCut={handleCut}
          confirmDelete={confirmDelete}
        />
        <div className={styles.divChild}>
          <small className={styles.lblTimeStampCalendarNoWrap}>
            {timeStamp} ({duration} mins){genre}
          </small>
          <br />
          <b style={{ color: '#0c212b' }} className={styles.lblTitleCalendar}>
            {title.titleName}
          </b>
          <small className={styles.txtGradeCertificate}>
            {gradeCertificateString}
          </small>

          <div style={{ marginTop: '10px' }}>
            {pin.includes('PIN 15') && (
              <img src={pin15Icon} style={{ height: '20px' }} title="PIN 15" />
            )}
            {pin.includes('PIN 12') && (
              <img src={pin12Icon} style={{ height: '20px' }} title="PIN 12" />
            )}
            {title.freePlay && (
              <img
                src={freePlayIcon}
                style={{ height: '20px' }}
                title="Free play"
              />
            )}
            {title.sponsorship && (
              <img
                src={sponsorshipIcon}
                style={{ height: '22px' }}
                title="Sponsorship"
              />
            )}
          </div>
        </div>
      </div>
    );
  };

  const computeHeight = (props) => {
    const slots = props.slots;
    let labeledSlots = 0;
    slots.map(function(value) {
      if (value.start.getMinutes() === 0) labeledSlots += 1;
    });
    const computedLabeledHeight = labeledSlots * 12;
    const computedHeight = (slots.length - labeledSlots) * 7;
    const totalHeight = computedLabeledHeight + computedHeight + 1;
    return totalHeight + 'px';
  };

  const CustomItem = (props) => (
    <div onClick={() => handleSelectSlot(props)}>
      <SchedulerItem
        {...props}
        children={sampleDiv(props)}
        resizeHint={false}
        style={{
          ...props.style,
          backgroundColor: '#fff',
          color: '#0c212b',
          maxHeight: computeHeight(props),
          display: computeHeight(props) == '0px' ? 'none' : 'block',
        }}
      />
    </div>
  );

  const CustomViewSlot = (props) => (
    <SchedulerSlot
      {...props}
      style={{
        minHeight: props.isAllDay ? undefined : 7,
        height: props.isAllDay ? undefined : 7,
        maxHeight: props.isAllDay ? undefined : 7,
      }}
    />
  );

  const handleDataChange = ({ created, updated, deleted }) => {
    setIsDirty(true);
    if (updated.length > 0) {
      const durationSplit = updated[0].duration.split(':');
      const minutesDuration =
        parseInt(durationSplit[0]) * 60 + parseInt(durationSplit[1]);
      updated[0].end = parseAdjust(
        DateService.addMinutes(updated[0].start, minutesDuration)
      );
      updated[0].startDate = parseAdjust(
        DateService.addHours(updated[0].start, clockStart * -1)
      );
      if (
        updated[0].startDate < dateRange.start ||
        updated[0].startDate >
          parseAdjust(DateService.addHours(dateRange.end, clockStart * -1))
      )
        return;
    }
    setHistory((old) => old.concat({ data: sampleData }));
    setSampleData((old) =>
      old
        // Filter the deleted items
        .filter(
          (item) =>
            deleted.find((current) => current.id === item.id) === undefined
        )
        // Find and replace the updated items
        .map(
          (item) => updated.find((current) => current.id === item.id) || item
        )
        // Add the newly created items and assign an `id`.
        .concat(
          created.map((item) =>
            Object.assign({}, item, {
              id: guid(),
            })
          )
        )
    );
  };

  const clickAddProgram = () => {
    setIsAddProgram(true);
  };

  const closeAddProgram = () => {
    setIsAddProgram(false);
  };

  const addProgram = (created) => {
    const duplicates = sampleData.filter((item) =>
      moment(created.start).isSame(item.start)
    );
    if (duplicates.length < 2) {
      setHistory((old) => old.concat({ data: sampleData }));
      setSampleData((old) => old.concat(created));
    } else {
      danger('Maximum Slot reached');
    }
    setIsAddProgram(false);
    closeModal();
    setIsDirty(true);
  };

  const editProgram = (updated) => {
    setHistory((old) => old.concat({ data: sampleData }));
    setSampleData((old) =>
      old.filter((item) => selectedSlot.dataItem.id !== item.id)
    );
    setSampleData((old) => old.concat(updated));
    closeModal();
    setIsDirty(true);
  };

  const handleNextWeek = () => {
    const nextDate = DateService.addHours(displayDate, 168);
    setDisplayDate(new Date(nextDate));
  };

  const handlePreviousWeek = () => {
    const nextDate = DateService.addHours(displayDate, -168);
    setDisplayDate(new Date(nextDate));
  };

  const compareDate = (startDate, endDate, dateNow) => {
    var range = moment(dateNow).isBetween(startDate, endDate);
    return range;
  };

  const formatScheduleDate = (date) => {
    const day = parseInt(date.split('/')[0]);
    const month = parseInt(date.split('/')[1]) - 1;
    const year = parseInt(date.split('/')[2]);

    const utcDate = new Date(Date.UTC(year, month, day, 0, 0, 0));

    return utcDate;
  };

  const [newlyAddedScheduleId, setNewlyAddedScheduleId] = useState();

  const constructScheduleParameters = () => {
    const startDate = formatScheduleDate(scheduleInfo.period.split('-')[0]);
    const endDate = formatScheduleDate(scheduleInfo.period.split('-')[1]);
    const slots = sampleData.map((item) => {
      const startTime = new Date(item.start);
      startTime.setHours(startTime.getHours() - clockStart);
      const utcDate = new Date(
        Date.UTC(
          startTime.getFullYear(),
          startTime.getMonth(),
          startTime.getDate(),
          startTime.getHours(),
          startTime.getMinutes()
        )
      );
      return {
        start: utcDate,
        titleName: item.title.titleName,
        duration: item.duration,
        fixed: item.fixed,
        live: item.title.live,
        pin: item.title.pin,
      };
    });
    const scheduleParameters = {
      startDate,
      endDate,
      channelId: scheduleInfo.channelID,
      slots,
    };

    return scheduleParameters;
  };

  const handleSaveSchedule = (isOp2mise) => {
    const scheduleParameters = constructScheduleParameters();
    setIsScheduleCreated(true);
    ScheduleService.saveSchedule(scheduleParameters)
      .then((response) => {
        setIsDirty(false);
        setNewlyAddedScheduleId(response.data);
        setScheduleID(response.data);
        if (!generatedSchedule) {
          success('Schedule has been successfully saved');
        } else
          success(
            'A new schedule (ID ' +
              response.data +
              ') has been successfully saved'
          );

        if (showExportModal) handleExport(response.data);
        if (isOp2mise === true) op2miseSchedule(response.data);
      })
      .finally(() => saveButtonRef?.current?.setDisableSave(false));
  };

  const handleUpdateSchedule = (isOp2mise) => {
    const scheduleParameters = constructScheduleParameters();
    ScheduleService.updateSchedule({
      ...scheduleParameters,
      scheduleId: newlyAddedScheduleId || scheduleID,
    })
      .then(() => {
        success('Schedule has been successfully saved');
        setIsDirty(false);
        if (showExportModal) handleExport(scheduleID);
        if (isOp2mise === true)
          op2miseSchedule(newlyAddedScheduleId || scheduleID);
      })
      .finally(() => saveButtonRef?.current?.setDisableSave(false));
  };

  const onSaveSchedule = (isOp2mise) => {
    if (!saveButtonRef?.current?.disableSave) {
      saveButtonRef?.current?.setDisableSave(true);
      if (newlyAddedScheduleId || isScheduleCreated)
        handleUpdateSchedule(isOp2mise);
      else handleSaveSchedule(isOp2mise);
    }
  };

  const minuteMs = 900000;

  const constructDisplay = (scheduledata) => {
    setPeriod(scheduledata.period);
    setScheduleID(scheduledata.data.id);
    const periodString = scheduledata.data.period;
    const startStr = periodString.split('-')[0];
    const endStr = periodString.split('-')[1];

    const scheduleDate = new Date(
      parseInt(startStr.split('/')[2]),
      parseInt(startStr.split('/')[1]) - 1,
      parseInt(startStr.split('/')[0]),
      6,
      0,
      0
    );

    setDisplayDate(scheduleDate);
    setFilterSchedule(scheduledata.filter);
    setCurrentYear(parseInt(startStr.split('/')[2]));

    const endDate = new Date(
      parseInt(endStr.split('/')[2]),
      parseInt(endStr.split('/')[1]) - 1,
      parseInt(endStr.split('/')[0]),
      23,
      59,
      59
    );
    if (compareDate(scheduleDate, endDate, new Date())) {
      setDefaultAddDDate(new Date());
    } else {
      setDefaultAddDDate(scheduleDate);
    }
    setDateRange({
      start: new Date(DateService.addHours(scheduleDate, 0)),
      end: new Date(DateService.addHours(endDate, 0)),
    });
  };

  useEffect(() => {
    if (scheduleInfo.period) {
      const interval = setInterval(() => {
        if (sampleData.length > 0) onSaveSchedule();
      }, minuteMs);

      return () => clearInterval(interval);
    }
  }, [scheduleInfo.period, sampleData]);

  useEffect(() => {
    ScheduleService.getUserSession().then((response) => {
      if (response.data !== '')
        setPredefinedScheduleData(response.data.scheduler);
      else navigate(`${process.env.REACT_APP_SUBFOLDER}/schedule`);
    });
  }, []);

  useEffect(() => {
    const scheduledata =
      AuthService.getSelectedSchedule() || predefinedScheduleData;
    if (scheduledata) constructDisplay(scheduledata);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [predefinedScheduleData]);

  useEffect(() => {
    if (currentYear !== null) {
      setIsLoading(true);
      const scheduledata =
        AuthService.getSelectedSchedule() || predefinedScheduleData;
      if (scheduledata)
        getGeneratedScheduleDetails(scheduledata.data, scheduledata.filter);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentYear, predefinedScheduleData]);

  useEffect(() => {
    const datas = sampleData.map((dataItem, index) => ({
      id: index,
      start: parseAdjust(DateService.addHours(dataItem.startDate, clockStart)),
      end: parseAdjust(DateService.addHours(dataItem.endDate, clockStart)),
      startDate: dataItem.startDate,
      endDate: dataItem.endDate,
      title: dataItem.title,
      description: 'description',
      isAllDay: false,
      extendedProps: dataItem.extendedProps,
    }));
    setSampleData(datas);
    if (history.length > 0) {
      const hisotricdatas = history.map((dataItem, index) => ({
        id: index,
        start: parseAdjust(
          DateService.addHours(dataItem.startDate, clockStart)
        ),
        end: parseAdjust(DateService.addHours(dataItem.endDate, clockStart)),
        startDate: dataItem.startDate,
        endDate: dataItem.endDate,
        title: dataItem.title,
        description: 'description',
        isAllDay: false,
        extendedProps: dataItem.extendedProps,
      }));
      setHistory(hisotricdatas);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [clockStart]);

  useEffect(() => {
    var prev = DateService.addHours(displayDate, -168);
    var next = DateService.addHours(displayDate, 168);
    setDisablePrevious(!(new Date(prev) >= dateRange.start));
    const allowNext =
      displayDate.getDay() >= dateRange.end.getDay() &&
      displayDate < dateRange.end;
    setDisableNext(!(new Date(next) <= dateRange.end) && !allowNext);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [displayDate]);

  const computeDateTime = (selectedData) => {
    const duration = isGeneratedDuration
      ? selectedData.duration
      : selectedData.title.duration;
    const durationSplit = duration.split(':');
    const durationMinutes =
      parseInt(durationSplit[0]) * 60 + parseInt(durationSplit[1]);
    const startTime = parseAdjust(
      DateService.addHours(selectedData.startDate, clockStart)
    );
    const endTime = parseAdjust(
      DateService.addMinutes(startTime, durationMinutes)
    );
    return { start: startTime, end: endTime };
  };

  useEffect(() => {
    const datas = sampleData.map((dataItem, index) => ({
      id: dataItem.id,
      start: computeDateTime(dataItem).start,
      end: computeDateTime(dataItem).end,
      startDate: dataItem.startDate,
      endDate: dataItem.endDate,
      title: dataItem.title,
      description: 'description',
      isAllDay: false,
      extendedProps: dataItem.extendedProps,
      duration: dataItem.duration,
      durationCurrent: isGeneratedDuration
        ? dataItem.duration
        : dataItem.title.duration,
      fixed: dataItem.fixed,
    }));
    setSampleData(datas);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isGeneratedDuration]);

  //#region CUT AND PASTE
  const handleCut = (slotSelected) => {
    if (Object.keys(slotSelected).length > 0) {
      setCutSlot(slotSelected);
    }
  };

  const handleUndo = () => {
    let hisotricdatas = history;
    let popped = hisotricdatas.pop();
    setSampleData(popped.data);
    setHistory(hisotricdatas);
    setCutSlot({});
    setIsDirty(true);
  };

  const pasteSlot = (created) => {
    if (Object.keys(cutSlot).length > 0) {
      setHistory((old) => old.concat({ data: sampleData }));
      setSampleData((old) =>
        old
          // Filter the deleted items
          .filter((item) => cutSlot.uid !== item.id)
          .concat(created)
      );
      closeModal();
      setCutSlot({});
      setSelectedSlot({});
      setDuplicateActive(false);
      setIsDirty(true);
    }
  };

  //#endregion

  const confirmDelete = (slot) => {
    if (Object.keys(slot).length > 0) {
      setHistory((old) => old.concat({ data: sampleData }));
      setSampleData((old) =>
        old
          // Filter the deleted items
          .filter((item) => slot.uid !== item.id)
      );
      closeModal();
      setCutSlot({});
      setDuplicateActive(false);
      setSelectedSlot({});
      setIsDirty(true);
    }
  };

  const handleSelectSlot = (event) => {
    setSelectedSlot(event);
    setDuplicateActive(true);
  };

  const closeModal = () => {
    setOpenForm('');
  };

  const setScheduleData = (e) => {
    setSampleData(e);
    setIsDirty(true);
  };

  const op2miseSchedule = (op2miseID) => {
    let period = scheduleInfo.period.split('-');
    let start = period[0].replace(/ /g, '').split('/');
    let end = period[1].replace(/ /g, '').split('/');
    const param = {
      ChannelId: scheduleInfo.channelID,
      Start: DateService.toIsoDateFormat(
        new Date(start[2], parseInt(start[1]) - 1, start[0])
      ),
      End: DateService.toIsoDateFormat(
        new Date(end[2], parseInt(end[1]) - 1, end[0])
      ),
      ScheduleID: op2miseID,
    };
    ScheduleService.generateSchedules(param).then(() => {
      setOp2miseLoading(false);
      setOpenForm(formTypes.op2mise);
    });
  };

  const scheduleHeader = () => {
    return (
      <div className={`col-12`} style={{ display: 'inline-block' }}>
        <button
          className={styles.saveButton}
          style={{
            float: 'left',
            fontSize: '20px',
            fontWeight: 'bolder',
            padding: '10px 20px',
            borderRadius: '4px 0px 0px 4px',
          }}
          onClick={handlePreviousWeek}
          type="button"
          title="previous"
          disabled={disablePrevious}
        >
          <img src={leftIcon} style={{ height: '17px' }} />
        </button>
        <button
          className={styles.saveButton}
          style={{
            float: 'left',
            fontSize: '20px',
            fontWeight: 'bolder',
            padding: '10px 20px',
            borderRadius: '0px 4px 4px 0px',
          }}
          onClick={handleNextWeek}
          type="button"
          title="next"
          disabled={disableNext}
        >
          <img src={rightIcon} style={{ height: '17px' }} />
        </button>
        <button
          className={`${styles.saveButton} ${styles.floatRight} ${styles.marginRight5}`}
          style={{ float: 'right' }}
          onClick={clickAddProgram}
          type="button"
        >
          New Slot
        </button>
        <button
          className={`${styles.saveButton} ${styles.floatRight} ${styles.marginRight5}`}
          onClick={handleUndo}
          type="button"
          disabled={history.length <= 0}
        >
          Undo
        </button>
        <img
          className={`${styles.floatRight} ${styles.headerIcons}`}
          height="29px"
          title={
            isStandard ? 'View in broadcast time' : 'View in standard time'
          }
          src={isStandard ? standardTimeIcon : broadcastTimeIcon}
          onClick={() => setIsStandard(!isStandard)}
        />
        <img
          className={`${styles.floatRight} ${
            styles.headerIcons
          } ${(disabledExport || isExporting || sampleData.length <= 0) &&
            styles.disabledExport}`}
          height="29px"
          title="Export this schedule"
          src={downloadFileIcon}
          alt=""
          onClick={() => {
            if (!disabledExport && !isExporting && !(sampleData.length <= 0)) {
              checkDirtyExport();
            }
          }}
        />
        <img
          className={`${styles.floatRight} ${styles.headerIcons}`}
          height="29px"
          title="Import schedule"
          src={uploadFileIcon}
          alt=""
          onClick={() => setOpenForm(formTypes.importForm)}
        />
        <img
          style={{
            float: 'right',
            marginRight: '15px',
            height: '29px',
            cursor: 'pointer',
            marginTop: '5px',
          }}
          title={
            isGeneratedDuration
              ? 'View original titles duration'
              : 'View generated titles duration'
          }
          src={isGeneratedDuration ? generatedIcon : originalIcon}
          onClick={() => {
            if (isGeneratedDuration) setIsGeneratedDuration(false);
            else setIsGeneratedDuration(true);
          }}
          hidden={scheduleID === 0 || !scheduleID}
        />
      </div>
    );
  };

  return (
    <>
      <GeneratedScheduleBannerComponent
        componentName="Schedules"
        scheduleDetails={scheduleInfo}
      />
      <hr style={{ background: '#c8c8c8' }} />
      {isLoading ? (
        <div
          style={{
            height: 'calc(100vh - 200px)',
            position: 'relative!important',
          }}
        >
          <p
            style={{
              textAlign: 'center',
              position: 'absolute',
              top: '45%',
              left: '55%',
            }}
          >
            <i
              className={`${styles.faCircleNotchSpin} fas fa-circle-notch fa-spin`}
            />
          </p>
        </div>
      ) : (
        <>
          <div className={`col-12`}>
            <small
              className={`${styles.divScheduleScores} `}
              title={`ID ${scheduleID} ${scheduleScores}`}
            >
              {' '}
              {scheduleID > 0 && (
                <>
                  <b>
                    ID
                    {scheduleID}
                  </b>{' '}
                  {scheduleScores}{' '}
                </>
              )}
            </small>
          </div>
          {isStandard ? (
            <DefaultTimeComponent
              tableHeight={tableHeight}
              sampleData={sampleData}
              displayDate={displayDate}
              handleDateChange={handleDataChange}
              CustomViewSlot={CustomViewSlot}
              handleDataChange={handleDataChange}
              scheduleHeader={scheduleHeader}
              CustomItem={CustomItem}
              dateRange={dateRange}
              addProgram={addProgram}
              parseAdjust={parseAdjust}
              clockStart={clockStart}
            />
          ) : (
            <StandardTimeComponent
              tableHeight={tableHeight}
              sampleData={sampleData}
              displayDate={displayDate}
              handleDateChange={handleDataChange}
              CustomViewSlot={CustomViewSlot}
              handleDataChange={handleDataChange}
              scheduleHeader={scheduleHeader}
              CustomItem={CustomItem}
              dateRange={dateRange}
              addProgram={addProgram}
              parseAdjust={parseAdjust}
              clockStart={clockStart}
            />
          )}

          <div style={{ marginTop: '15px', float: 'right' }}>
            <button
              className={styles.cancelButton}
              onClick={callbackSchedule}
              type="button"
            >
              Close
            </button>
            <button
              className={styles.saveButton}
              style={{ marginLeft: '10px' }}
              onClick={() => {
                setOp2miseLoading(true);
                onSaveSchedule(true);
              }}
              disabled={
                (history.length <= 0 && !isScheduleCreated) ||
                (isScheduleCreated && sampleData.length == 0) ||
                op2miseLoading
              }
              type="button"
            >
              Op2mise
              {op2miseLoading && (
                <i
                  className="fas fa-circle-notch fa-spin"
                  style={{ marginLeft: '5px' }}
                />
              )}
            </button>
            <SaveButtonComponent
              ref={saveButtonRef}
              sampleData={sampleData}
              onSaveSchedule={onSaveSchedule}
            />
            {scheduleID > 0 && (
              <>
                <button
                  className={styles.saveButton}
                  style={{ marginLeft: '10px' }}
                  onClick={handleExportStatistics}
                  disabled={disabledStatistics}
                  type="button"
                  hidden={true}
                >
                  Statistics
                </button>

                <button
                  className={styles.saveButton}
                  onClick={submitSchedule}
                  disabled={disabledSubmit || isScheduleCreated}
                  style={{
                    marginLeft: '10px',
                  }}
                  type="button"
                >
                  Submit
                </button>
              </>
            )}
          </div>
        </>
      )}
      {isAddProgram && (
        <AddProgramsComponent
          showModal={isAddProgram}
          closeModal={closeAddProgram}
          addProgram={addProgram}
          parseAdjust={parseAdjust}
          formType={'A'}
          headerText={'New Slot'}
          clockStart={clockStart}
          displayDate={displayDate}
          dateRange={dateRange}
        />
      )}
      {openForm == formTypes.paste && (
        <PasteComponent
          closeModal={closeModal}
          pasteSlot={pasteSlot}
          parseAdjust={parseAdjust}
          dataItem={cutSlot.dataItem}
          clockStart={clockStart}
          dateRange={dateRange}
        />
      )}

      {openForm == formTypes.insert && (
        <AddProgramsComponent
          showModal={openForm == formTypes.insert}
          closeModal={closeModal}
          addProgram={addProgram}
          parseAdjust={parseAdjust}
          formType={formTypes.insert}
          selectedSlot={selectedSlot}
          headerText={'New Slot'}
          clockStart={clockStart}
          displayDate={displayDate}
          dateRange={dateRange}
        />
      )}
      {openForm == formTypes.edit && (
        <EditProgramsComponent
          closeModal={closeModal}
          editProgram={editProgram}
          parseAdjust={parseAdjust}
          formType={formTypes.edit}
          selectedSlot={selectedSlot}
          headerText={'Modify Slot'}
          editTitle={editTitle}
          setEditTitle={setEditTitle}
          clockStart={clockStart}
          dateRange={dateRange}
          formatScheduleDate={formatScheduleDate}
        />
      )}
      {openForm == formTypes.op2mise && (
        <ConfirmSubmitModalComponent
          closeModal={closeModal}
          headerText="Op2mise process started."
          message="A new generated schedule with the applied changes will be listed shortly."
        />
      )}
      {openForm == formTypes.submit && (
        <ConfirmSubmitModalComponent
          closeModal={closeModal}
          headerText="Schedule was successfully submitted."
          message="Please check submission, the submitted schedule will be listed there shortly."
        />
      )}
      <ConfirmLeaveModal
        show={showPrompt}
        onSave={() => {
          onSaveSchedule();
          confirmNavigation();
        }}
        onDiscard={confirmNavigation}
        onHide={cancelNavigation}
      />
      {openForm == formTypes.importForm && (
        <ImportSlotsComponent
          closeModal={closeModal}
          setScheduleData={(e) => setScheduleData(e)}
          scheduledata={sampleData}
          clockStart={clockStart}
          scheduleInfo={scheduleInfo}
        />
      )}
      <ExportModalComponent
        show={showExportModal}
        onSave={onSaveSchedule}
        onDiscard={() => {
          handleExport(scheduleID);
        }}
        onHide={() => setShowExportModal(false)}
      />
    </>
  );
}

export default SchedulerComponent;
