import React, { useState, useEffect, useRef } from 'react';
import { Dialog } from '@progress/kendo-react-dialogs';
import { Modal } from 'react-bootstrap';
import { guid } from '@progress/kendo-react-common';
import DateService from '../../../../core/date.service';
import Styles from './ScheduleForms.module.css';
import style from '../ReactSchedule.module.css';
import SchedulerStorage from '../SchedulerStorage';
import CustomDateInput from '../../../../shared/components/custom-date-input/CustomDateInput';
import CustomTimeInput from '../../../../shared/components/custom-time-input/CustomTimeInput';

const { formTypes } = SchedulerStorage;
export const PasteComponent = ({
  pasteSlot,
  closeModal,
  parseAdjust,
  dataItem,
  clockStart,
  dateRange,
}) => {
  const [dateValue, setDateValue] = useState();
  const [timeValue, setTimeValue] = useState();
  const [title, setTitle] = useState({});
  const [hasErrorValidation, setHasErrorValidation] = useState(false);
  const [exceedDate, setExceedDate] = useState(false);
  const [receedDate, setReceedDate] = useState(false);
  const handleSaveForm = () => {
    const date = dateValue;
    if (dateValue && timeValue) {
      date?.setHours(timeValue?.getHours());
      date?.setMinutes(timeValue?.getMinutes());
    }
    const newDate = new Date(date);
    setExceedDate(false);
    setReceedDate(false);
    setHasErrorValidation(false);
    let hasError = false;
    if (!dateValue || !timeValue) {
      setHasErrorValidation(true);
      hasError = true;
    } else if (newDate > dateRange.end) {
      setExceedDate(true);
      hasError = true;
    } else  if (newDate < dateRange.start) {
      setReceedDate(true);
      hasError = true;
    }

    if (!hasError) {
      setHasErrorValidation(false);
      const selectedProgram = dataItem.title;
      const durationSplit = selectedProgram.duration.split(':');
      const durationMinutes =
        parseInt(durationSplit[0]) * 60 + parseInt(durationSplit[1]);
      const startTime = DateService.addHours(newDate, clockStart);
      const endTime = DateService.addMinutes(startTime, durationMinutes);
      const slot = {
        id: guid(),
        start: new Date(startTime),
        end: new Date(endTime),
        title: selectedProgram,
        description: 'description',
        isAllDay: false,
        startDate: newDate,
        endDate: DateService.addMinutes(newDate, durationMinutes),
        duration: selectedProgram.duration,
        durationCurrent: selectedProgram.duration,
      };
      pasteSlot(slot);
    }
  };

  const onChangeDate = (value) => {
    setDateValue(value);
  };

  const onChangeTime = (value) => {
    setTimeValue(value);
  };

  const clearValidation = () => {
    setValidation({ errorDateTime: false, errorTitle: false });
  };

  useEffect(() => {
    const date = parseAdjust(DateService.addHours(dataItem.start, 6));
    const time = new Date();
    time.setTime(date.getTime());
    setTitle(dataItem.title);
    setDateValue(date);
    setTimeValue(new Date(time));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <Dialog
      className="pasteDialog"
      title={'Paste'}
      onClose={closeModal}
      width={650}
      closeIcon={null}
    >
      <div>
        <h5 style={{ display: 'inline' }}>
          {dataItem.title.titleName}
        </h5>
      </div>
      <div style={{ display: 'inline-grid' }}>
        <small className={Styles.smallLabel}>
          Duration: <strong>{title.duration}</strong>
        </small>
        <small className={Styles.smallLabel}>
          Grade: <strong>{title.grade}</strong>
        </small>
        <small className={Styles.smallLabel}>
          Certificate: <strong>{title.certificate}</strong>
        </small>
      </div>
      <div style={{ marginTop: '20px' }}>
        <div className="d-flex customDatePickerWidth">
          <div className={`${Styles.startDateDiv} start-date-div`}>
            <div className={Styles.startDivLabel}>Start date</div>
            <CustomDateInput
              value={dateValue}
              min={dateRange.start}
              max={dateRange.end}
              onChange={onChangeDate}
            />
          </div>
          <div className={`${Styles.startTimeDiv} ${Styles.startTimeInputCut} start-time-div`}>
            <div className={Styles.startDivLabel}>Start time</div>
            <CustomTimeInput
              value={timeValue}
              onChange={onChangeTime}
            />
          </div>
        </div>
        <small class="errorValidation" hidden={!hasErrorValidation}>
          Please select a date and time to paste the slot.
        </small>
        <small class="errorValidation" hidden={!exceedDate}>
          Exceeded date period.
        </small>
        <small class="errorValidation" hidden={!receedDate}>
          Start date should not be earlier than the period start date.
        </small>
      </div>
      <div style={{ textAlign: 'center', marginTop: '-15px', float: 'right' }}>
        <button className={`${Styles.cancelButton}`} onClick={closeModal}>
          Cancel
        </button>
        <button className={`${Styles.saveButton}`} onClick={handleSaveForm}>
          Paste
        </button>
      </div>
    </Dialog>
  );
};

export const ConfirmSubmitModalComponent = ({
  closeModal,
  headerText,
  message,
}) => {
  return (
    <Modal size="md" show={true} aria-labelledby="example-modal-sizes-title-md">
      <Modal.Body style={{ padding: '35px' }}>
        <div style={{ textAlign: 'center' }}>{headerText}</div>
        <div style={{ textAlign: 'center' }}>{message}</div>
        <div style={{ textAlign: 'center', paddingTop: '30px' }}>
          <button
            className={`${Styles.saveButton}`}
            style={{ width: '100%' }}
            onClick={closeModal}
          >
            Close
          </button>
        </div>
      </Modal.Body>
    </Modal>
  );
};

export const SlotDropdownComponent = ({
  slot,
  handleDuplicate,
  setSelectedSlot,
  setOpenForm,
  setEditTitle,
  handleCut,
  confirmDelete,
}) => {
  const [showDropdownMenu, setShowDropdownMenu] = useState(false);
  const contextMenuRef = useRef(null);
  const showDropdown = (e) => {
    e.stopPropagation();
    setShowDropdownMenu(!showDropdownMenu);
  };

  useEffect(() => {
    const backdropClicked = (e) => {
      if (
        contextMenuRef.current &&
        !contextMenuRef.current.contains(e.target)
      ) {
        setShowDropdownMenu(false);
      }
    };

    document.addEventListener('mousedown', backdropClicked);
    return () => document.removeEventListener('mousedown', backdropClicked);
  }, [contextMenuRef]);

  return (
    <div ref={contextMenuRef}>
      <div className={`${style.dropdown}`} onClick={showDropdown}>
        ...
      </div>
      {showDropdownMenu && (
        <div className={`${style.dropdownContent}`}>
          <small
            className={`${Styles.optionItem}`}
            onClick={() => handleDuplicate(slot)}
            title="Duplicate"
          >
            Duplicate
          </small>
          <small
            className={`${Styles.optionItem}`}
            onClick={() => {
              confirmDelete(slot);
            }}
            title="Delete"
          >
            Delete
          </small>
          <small
            className={`${Styles.optionItem}`}
            title="Modify"
            onClick={() => {
              setSelectedSlot(slot);
              setEditTitle(slot.dataItem.title);
              setOpenForm(formTypes.edit);
            }}
          >
            Modify
          </small>
          <small
            className={`${Styles.optionItem}`}
            onClick={() => {
              handleCut(slot);
              setOpenForm(formTypes.paste);
            }}
            title="Cut"
          >
            Cut
          </small>
        </div>
      )}
    </div>
  );
};
