import React, { useState, useEffect } from 'react';
import { Modal } from 'react-bootstrap';
import styles from './SuccessModal.module.css';
import xIcon from '../../../../../assets/icons/X.svg';
import checkIcon from '../../../../../assets/icons/Check.svg';
import SchedulerStorage from '../../SchedulerStorage';
import { downloadFileIcon } from '../../../../../assets';
import { saveAs } from 'file-saver';
import XLSX from 'sheetjs-style';
import AuthService from '../../../../../core/auth.service';

export default function ImportSuccessModalComponent({
  closeModal,
  uploadedList,
  scheduleInfo,
}) {
  const { importFilter } = SchedulerStorage;
  const [filter, setFilter] = useState(importFilter.all);
  const [tableData, setTableData] = useState([]);

  const changeFilter = (selected) => {
    setFilter(selected);
  };

  const filterUploaded = () => {
    if (filter === importFilter.all) setTableData(uploadedList);
    else if (filter === importFilter.Imported)
      setTableData(uploadedList.filter((o) => o.imported));
    else if (filter === importFilter.NotImported)
      setTableData(uploadedList.filter((o) => !o.imported));
  };

  const exportExcel = () => {
    const { organisationName } = AuthService.getUserIdentity();
    let excelData = tableData.map((value, index) => {
      return {
        'Programme-title': value.title,
        Duration: value.duration,
        'Schedule-date': value.date,
        'Start-time': value.startTime,
        Imported: value.imported,
        Result: value.result,
      };
    });
    const fileType =
      'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet';
    const fileName =
      organisationName +
      '_op2mise_schedule_import_results_' +
      scheduleInfo.channelName +
      '_' +
      scheduleInfo.period
        .split('-')[0]
        .replaceAll('/', '')
        .replace(' ', '') +
      '_' +
      scheduleInfo.period
        .split('-')[1]
        .replaceAll('/', '')
        .replace(' ', '') +
      '_' +
      String(new Date().getDate()).padStart(2, '0') +
      String(new Date().getMonth() + 1).padStart(2, '0') +
      new Date().getFullYear() +
      '.xlsx';
    const ws = XLSX.utils.json_to_sheet(excelData);
    const wb = { Sheets: { errors: ws }, SheetNames: ['errors'] };
    const excelBuffer = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });
    const data = new Blob([excelBuffer], { type: fileType });
    saveAs(data, fileName);
  };

  useEffect(() => {
    filterUploaded();
  }, [filter, uploadedList]);

  return (
    <>
      <Modal
        size="lg"
        className="modal-widthCustom"
        show={true}
        onHide={() => setModalSuccessShow(false)}
        aria-labelledby="example-modal-sizes-title-lg"
      >
        <Modal.Body style={{ padding: '35px' }}>
          <div className={styles.divMessage}>
            <b> {uploadedList.filter((o) => o.imported).length} </b>
            slots out of <b> {uploadedList.length} </b>
            were successfully imported and validated!
          </div>

          <div className={`${styles.successDiv}`}>
            <table>
              <thead className={styles.tableHead}>
                <tr className={styles.tableRow} style={{ width: '100%' }}>
                  <th className={`${styles.tableHeader} ${styles.width22}`}>
                    PROGRAMME TITLE
                  </th>
                  <th className={`${styles.tableHeader} ${styles.width13}`}>
                    DURATION
                  </th>
                  <th className={`${styles.tableHeader} ${styles.width11}`}>
                    DATE
                  </th>
                  <th className={`${styles.tableHeader} ${styles.width13}`}>
                    START TIME
                  </th>
                  <th className={`${styles.tableHeader} ${styles.width6}`}></th>
                  <th className={`${styles.tableHeader} ${styles.width35}`}>
                    RESULTS
                  </th>
                </tr>
              </thead>
              <tbody className={`${styles.successBody} ${styles.tableBody}`}>
                {tableData.map((value) => (
                  <tr
                    key={value.id}
                    className={`${styles.trBorder} ${styles.tableRow}`}
                    style={{ width: '100%' }}
                  >
                    <td className={`table-body-column ${styles.width21}`}>
                      {value.title}
                    </td>
                    <td className={`table-body-column ${styles.width12}`}>
                      {value.duration}
                    </td>
                    <td className={`table-body-column ${styles.width12}`}>
                      {value.date}
                    </td>
                    <td className={`table-body-column ${styles.width12}`}>
                      {value.startTime}
                    </td>
                    <td className={`table-body-column ${styles.width5}`}>
                      <i>
                        <img src={value.imported ? checkIcon : xIcon} alt="" />
                      </i>
                    </td>
                    <td
                      className={`table-body-column  ${styles.bold} ${
                        styles.width32
                      } ${
                        value.result === 'Success!'
                          ? styles.success
                          : styles.error
                      }`}
                    >
                      {value.result}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
          <div className={styles.divSelection}>
            <div
              className="form-check"
              style={{ marginRight: '20px', display: 'inline-block' }}
              onClick={() => changeFilter(importFilter.all)}
              onKeyDown={() => changeFilter(importFilter.all)}
              role="presentation"
            >
              <input
                className={`${styles.formCheck} form-check-input`}
                type="radio"
                name="successFilter"
                id="flexRadioDefault1"
                checked={filter === importFilter.all}
              />
              <div className="form-check-label" htmlFor="flexRadioDefault1">
                Show all
              </div>
            </div>

            <div
              className="form-check"
              style={{ marginRight: '20px', display: 'inline-block' }}
              onClick={() => changeFilter(importFilter.Imported)}
              onKeyDown={() => changeFilter(importFilter.Imported)}
              role="presentation"
            >
              <input
                className={`${styles.formCheck} form-check-input`}
                type="radio"
                name="successFilter"
                id="flexRadioDefault2"
                checked={filter === importFilter.Imported}
              />
              <div className="form-check-label" htmlFor="flexRadioDefault2">
                Imported
              </div>
            </div>

            <div
              className="form-check"
              style={{ marginRight: '20px', display: 'inline-block' }}
              onClick={() => changeFilter(importFilter.NotImported)}
              onKeyDown={() => changeFilter(importFilter.NotImported)}
              role="presentation"
            >
              <input
                className={`${styles.formCheck} form-check-input`}
                type="radio"
                name="successFilter"
                id="flexRadioDefault3"
                checked={filter === importFilter.NotImported}
              />
              <div className="form-check-label" htmlFor="flexRadioDefault3">
                Not imported
              </div>
            </div>

            <img
              height="29px"
              style={{ float: 'right', cursor: 'pointer' }}
              title="Download results"
              src={downloadFileIcon}
              alt=""
              onClick={exportExcel}
            />
          </div>

          <div className={styles.divClose}>
            <button
              className={styles.saveButton}
              onClick={closeModal}
              style={{ marginRight: '0' }}
              type="button"
            >
              Close
            </button>
          </div>
        </Modal.Body>
      </Modal>
    </>
  );
}
