import React, { useState, useEffect } from 'react';
import Styles from './ScheduleForms.module.css';

export default function LiveComponent({
  regularSlot,
  validation,
  liveNameValue,
  setLiveNameValue,
}) {
  const onChangeLiveName = (e) => {
    setLiveNameValue(e.target.value);
  };
  return (
    <div className={`m-1`}>
      <div class="form-group col-12">
        <label className={`col-10`}>
          {regularSlot ? 'Slot name' : 'Series name'}
        </label>
        <input
          className={`${Styles.durationInput}`}
          type="text"
          value={liveNameValue}
          placeholder={
            regularSlot ? 'Enter the slot name' : 'Enter the series name'
          }
          maxLength={100}
          onChange={onChangeLiveName}
        />
        {validation.errorLiveName && (
          <small class="errorValidation">
            Invalid name {regularSlot ? 'Slot name' : 'Series name'}
          </small>
        )}
      </div>
    </div>
  );
}
