import React, { useState, useEffect } from 'react'
import Styles from '../../ReactSchedule.module.css'
import SelectComponent from '../../../../../shared/elements/select/select.component'
import titleGroupStyle from './TitleGroupTable.module.css'
import ScheduleService from '../../../ScheduleService'

function TitleGroupSelectComponent({ onSelect }) {
  const [selectedTitleGroup, setSelectedTitleGroup] = useState({
    id: 0,
    name: 'Select Title Group',
  })
  const [titleGroups, setTitleGroups] = useState([])

  const selectTitleGroup = (e) => {
    setSelectedTitleGroup(e)
    onSelect(e.id)
  }

  const loadTitleGroups = () => {
    ScheduleService.getTitleGroups().then((response) => {
      if (response.data.data.length == 0) {
        setSelectedTitleGroup({ id: 0, name: 'No available title group' })
      } else {
        setTitleGroups(response.data.data)
        selectTitleGroup(response.data.data[0])
      }
    })
  }

  useEffect(() => {
    loadTitleGroups()
  }, [])

  return (
    <div className={Styles.searchWrapper}>
      <div className={`pb-2 pt-2 ${titleGroupStyle.titleGroupHeader}`}>
        <div className={`pt-2 ${titleGroupStyle.w10}`}>Title Group:</div>
        <div className={`${titleGroupStyle.w90}`}>
          <SelectComponent
            text={selectedTitleGroup.name}
            list={titleGroups}
            onSelect={selectTitleGroup}
            width='100%'
            withSearch
          />
        </div>
      </div>
    </div>
  )
}

export default TitleGroupSelectComponent
