import { Modal } from 'react-bootstrap';
import styles from '../../../../shared/components/confirm-leave-modal/styles.module.css';
import Button from '../../../../shared/components/button/Button';

const ExportModalComponent = ({ show, onSave, onDiscard, onHide }) => {
  return (
    <Modal
      size="md"
      show={show}
      onHide={onHide}
      aria-labelledby="confirm-export-modal"
    >
      <Modal.Body className={styles.promptModalBody}>
        <div>
          You have unsaved changes to this schedule. Exporting the current
          version will not include these changes. Do you want to save your
          changes before exporting or proceed with the current version?
        </div>
        <div>
          <div className={styles.button}>
            <Button
              text="Save and export"
              onClick={onSave}
              style={{ width: '100%' }}
            />
          </div>
          <div className={styles.button}>
            <Button
              text="Discard changes and export"
              onClick={onDiscard}
              style={{ width: '100%' }}
            />
          </div>
          <div className={styles.button}>
            <Button
              text="Cancel"
              defaultBtn
              onClick={onHide}
              style={{ width: '100%' }}
            />
          </div>
        </div>
      </Modal.Body>
    </Modal>
  );
};

export default ExportModalComponent;
