import React, { useState, useEffect, useRef } from 'react';
import { guid } from '@progress/kendo-react-common';
import SelectProgramsTableComponent from './SelectProgramsTableComponent';
import { Dialog } from '@progress/kendo-react-dialogs';
import DateService from '../../../../core/date.service';
import Styles from './ScheduleForms.module.css';
import ReccurencePatternComponent from './ReccurencePatternComponent';
import TabsComponent from '../../../_reusable/tabs/TabsComponent';
import SelectTitleGroupTableComponent from './tables/SelectTitleGroupTableComponent';
import SwitchComponent from '../../../../shared/elements/switch/switch.component';
import SchedulerStorage from '../SchedulerStorage';
import LiveComponent from './LiveComponent';
import CustomDateInput from '../../../../shared/components/custom-date-input/CustomDateInput';
import CustomTimeInput from '../../../../shared/components/custom-time-input/CustomTimeInput';
export const AddProgramsComponent = ({
  showModal,
  closeModal,
  addProgram,
  parseAdjust,
  formType,
  selectedSlot,
  headerText,
  clockStart,
  displayDate,
  defaultAddDate,
  dateRange,
}) => {
  const sunday = 0,
    monday = 1,
    tuesday = 2,
    wednesday = 3,
    thursday = 4,
    friday = 5,
    saturday = 6;
  const startDateRef = useRef();
  const tableHeights = SchedulerStorage.addFormTableHeights;
  const [activeTab, setActiveTab] = useState('Titles');
  const [selectedTab, setSelectedTab] = useState('Titles');
  const [dateTimeValue, setDateTimeValue] = useState(new Date());
  const [dateValue, setDateValue] = useState();
  const [timeValue, setTimeValue] = useState();
  const [durationValue, setDurationValue] = useState('');
  const [numberOfSlotsValue, setNumberOfSlotsValue] = useState('1');
  const [liveNameValue, setLiveNameValue] = useState('');
  const [isRegularSlot, setIsRegularSlot] = useState(true);
  const [selectedProgram, setSelectedProgram] = useState({});
  const [selectedProgramGroup, setSelectedProgramGroup] = useState([]);
  const [recurrenceParameters, setRecurrenceParameters] = useState({});
  const [
    currentDayFormIncrementDate,
    setCurrentDayFormIncrementDate,
  ] = useState();
  const [validation, setValidation] = useState(
    SchedulerStorage.addSlotValidation
  );
  const [fixed, setFixed] = useState(false);
  const [live, setLive] = useState(false);
  const [hasTitles, setHasTitles] = useState(false);
  const durationRef = useRef(null);
  const numberSlotRef = useRef(null);
  const pushToNextWeekIfReachedWeekend = (submit) => {
    let date = new Date(submit);
    let day = date.getDay();

    if (day == saturday) date.setDate(date.getDate() + 2);
    if (day == sunday) date.setDate(date.getDate() + 1);
    return new Date(date);
  };

  const pushToWeekEndsIfWithinWeekdays = (submit) => {
    let date = new Date(submit);
    let day = date.getDay();

    if (day == monday) date.setDate(date.getDate() + 5);
    if (day == tuesday) date.setDate(date.getDate() + 4);
    if (day == wednesday) date.setDate(date.getDate() + 3);
    if (day == thursday) date.setDate(date.getDate() + 2);
    if (day == friday) date.setDate(date.getDate() + 1);
    return new Date(date);
  };

  const getDateNextWeek = (submit) => {
    let date = new Date(submit);
    let day = date.getDay();
    const daysToNextWeek = day === sunday ? monday : 8 - day;
    const coupleMoreWeeks = recurrenceParameters.everyWeekInterval * 7 - 7;
    date.setDate(date.getDate() + daysToNextWeek + coupleMoreWeeks);
    return new Date(date);
  };

  const pushToSpecificDayPerWeek = (submit, daysIndex) => {
    let date = new Date(submit);
    const day = date.getDay();
    const daysToStop = recurrenceParameters.days;
    const daysToAdd =
      daysToStop[daysIndex] === 0 ? 6 : daysToStop[daysIndex] - day;
    date.setDate(date.getDate() + daysToAdd);
    return new Date(date);
  };

  const incrementDays = (submit, slotIndex) => {
    let date = new Date(submit);
    let daysToAdd = 1;

    switch (recurrenceParameters.intervalId) {
      case 1:
        daysToAdd = recurrenceParameters.intervalValue;
        if (slotIndex > 0)
          date = new Date(date.setDate(date.getDate() + parseInt(daysToAdd)));
        break;
      case 2:
        if (slotIndex === 0) date = pushToNextWeekIfReachedWeekend(date);
        if (slotIndex > 0)
          date = pushToNextWeekIfReachedWeekend(
            new Date(date.setDate(date.getDate() + 1))
          );
        break;
      case 3:
        if (slotIndex === 0) date = pushToWeekEndsIfWithinWeekdays(date);
        if (slotIndex > 0)
          date = pushToWeekEndsIfWithinWeekdays(
            new Date(date.setDate(date.getDate() + 1))
          );
        break;
      default:
        break;
    }

    return date;
  };

  const incrementByDuration = (submit, hoursToAdd, minutesToAdd, slotIndex) => {
    const totalMinutes = hoursToAdd * 60 + minutesToAdd;
    let date = new Date(submit);
    if (slotIndex > 0)
      date = new Date(date.setMinutes(date.getMinutes() + totalMinutes));
    return date;
  };

  const addRecurrencePattern = (
    date,
    duration,
    slotIndex,
    daysIndex,
    dateNextWeek
  ) => {
    switch (recurrenceParameters.reccurencePatternId) {
      case 2:
        return incrementDays(date, slotIndex);
      case 3:
        return pushToSpecificDayPerWeek(dateNextWeek, daysIndex);
      default:
        return incrementByDuration(
          date,
          parseInt(duration[0], 10),
          parseInt(duration[1], 10),
          slotIndex
        );
    }
  };

  const addSeriesSlot = (durationSplit) => {
    let lastDateTimeValue = dateTimeValue;
    const previousweek = new Date(
      lastDateTimeValue.getTime() -
        7 * recurrenceParameters.everyWeekInterval * 24 * 60 * 60 * 1000
    );
    let dateNextWeek = getDateNextWeek(previousweek);
    let day = dateTimeValue.getDay();
    let daysIndex = recurrenceParameters.days?.findIndex((o) => o === day);
    if (daysIndex === -1 && recurrenceParameters.reccurencePatternId === 3) {
      do {
        if (day === 7 || day === 0) {
          day = day === 7 ? 0 : day + 1;
          dateNextWeek = getDateNextWeek(dateNextWeek);
        } else {
          day = day + 1;
          day = day === 7 ? 0 : day;
        }

        daysIndex = recurrenceParameters.days?.findIndex((o) => o === day);
      } while (daysIndex === -1);
    }

    var durationToSave =
      durationSplit?.every((o) => o) &&
      durationSplit[0].padStart(2, '0') +
        ':' +
        durationSplit[1].padStart(2, '0');
    const slotsLength =
      !isRegularSlot &&
      !live &&
      numberOfSlotsValue &&
      numberOfSlotsValue <= selectedProgramGroup.length
        ? numberOfSlotsValue
        : selectedProgramGroup.length;

    for (var i = 0; i < slotsLength; i++) {
      const durationMinutes =
        parseInt(durationSplit[0]) * 60 + parseInt(durationSplit[1]);
      const defaultDurationMinutes =
        parseInt(selectedProgramGroup[i].duration.split(':')[0]) * 60 +
        parseInt(selectedProgramGroup[i].duration.split(':')[1]);
      const defaultDurationToSave = selectedProgramGroup[i].duration;
      const defaultDurationSplit =
        selectedProgramGroup[i === 0 ? i : i - 1].duration;
      const duration = durationToSave
        ? durationMinutes
        : defaultDurationMinutes;
      const timeSplit = durationToSave
        ? durationSplit
        : defaultDurationSplit.split(':');

      const newDateTimeValue = addRecurrencePattern(
        lastDateTimeValue,
        timeSplit,
        i,
        daysIndex,
        dateNextWeek
      );
      lastDateTimeValue = addRecurrencePattern(
        lastDateTimeValue,
        timeSplit,
        i,
        daysIndex,
        dateNextWeek
      );

      if (newDateTimeValue > new Date(dateRange.end)) break;

      const startTime = DateService.addHours(newDateTimeValue, clockStart);
      const endTime = DateService.addMinutes(startTime, duration);
      const slot = {
        id: guid(),
        start: parseAdjust(startTime),
        end: parseAdjust(endTime),
        startDate: newDateTimeValue,
        endDate: DateService.addMinutes(newDateTimeValue, duration),
        title: selectedProgramGroup[i],
        description: 'description',
        isAllDay: false,
        duration: durationToSave || defaultDurationToSave,
        durationCurrent: durationToSave || defaultDurationToSave,
        fixed: fixed,
      };
      addProgram(slot);

      if (recurrenceParameters.reccurencePatternId === 3) {
        if (daysIndex === recurrenceParameters.days.length - 1) {
          daysIndex = 0;
          dateNextWeek = getDateNextWeek(dateNextWeek);
        } else daysIndex = daysIndex + 1;
      }
    }

    setRecurrenceParameters({});
  };

  const addRegularSlot = (durationSplit) => {
    if (selectedTab === 'Titles') {
      const durationMinutes =
        parseInt(durationSplit[0]) * 60 + parseInt(durationSplit[1]);
      const startTime = DateService.addHours(dateTimeValue, clockStart);
      const endTime = DateService.addMinutes(startTime, durationMinutes);
      var durationToSave =
        durationSplit[0].padStart(2, '0') +
        ':' +
        durationSplit[1].padStart(2, '0');
      const slot = {
        id: guid(),
        start: parseAdjust(startTime),
        end: parseAdjust(endTime),
        startDate: dateTimeValue,
        endDate: DateService.addMinutes(dateTimeValue, durationMinutes),
        title: selectedProgram,
        description: 'description',
        isAllDay: false,
        duration: durationToSave,
        durationCurrent: durationToSave,
        fixed: fixed,
        type: 'non-live',
      };
      addProgram(slot);
    }

    if (selectedTab === 'Title Groups') {
      addSeriesSlot(durationSplit);
    }
  };

  const addLiveSlot = (durationSplit) => {
    const durationMinutes =
      parseInt(durationSplit[0]) * 60 + parseInt(durationSplit[1]);
    var startTime = DateService.addHours(dateTimeValue, clockStart);
    var endTime = DateService.addMinutes(startTime, durationMinutes);
    var dateAdjustedValue = dateTimeValue;
    var durationToSave =
      durationSplit[0].padStart(2, '0') +
      ':' +
      durationSplit[1].padStart(2, '0');
    if (isRegularSlot) {
      const slot = {
        id: guid(),
        start: parseAdjust(startTime),
        end: parseAdjust(endTime),
        startDate: dateAdjustedValue,
        endDate: DateService.addMinutes(dateAdjustedValue, durationMinutes),
        title: {
          titleName: liveNameValue,
          duration: durationValue,
          live: live,
        },
        description: 'description',
        isAllDay: false,
        duration: durationToSave,
        durationCurrent: durationToSave,
        fixed: fixed,
        type: 'live',
      };
      addProgram(slot);
    } else {
      let lastDateTimeValue = dateTimeValue;
      const previousweek = new Date(
        lastDateTimeValue.getTime() - 7 * 24 * 60 * 60 * 1000
      );
      let dateNextWeek = getDateNextWeek(previousweek);
      let day = dateTimeValue.getDay();
      let daysIndex = recurrenceParameters.days?.findIndex((o) => o === day);
      if (daysIndex === -1 && recurrenceParameters.reccurencePatternId === 3) {
        do {
          if (day === 7 || day === 0) {
            day = day === 7 ? 0 : day + 1;
            dateNextWeek = getDateNextWeek(dateNextWeek);
          } else {
            day = day + 1;
          }

          daysIndex = recurrenceParameters.days?.findIndex((o) => o === day);
        } while (daysIndex === -1);
      }
      for (var i = 0; i < parseInt(numberOfSlotsValue, 10); i++) {
        const durationMinutes =
          parseInt(durationSplit[0]) * 60 + parseInt(durationSplit[1]);
        const newDateTimeValue = addRecurrencePattern(
          lastDateTimeValue,
          durationSplit,
          i,
          daysIndex,
          dateNextWeek
        );
        lastDateTimeValue = addRecurrencePattern(
          lastDateTimeValue,
          durationSplit,
          i,
          daysIndex,
          dateNextWeek
        );

        if (newDateTimeValue > new Date(dateRange.end)) break;

        const startTime = DateService.addHours(newDateTimeValue, clockStart);
        const endTime = DateService.addMinutes(startTime, durationMinutes);
        const slot = {
          id: guid(),
          start: parseAdjust(startTime),
          end: parseAdjust(endTime),
          startDate: newDateTimeValue,
          endDate: DateService.addMinutes(dateTimeValue, durationMinutes),
          title: {
            titleName: liveNameValue,
            duration: durationValue,
            live: live,
          },
          description: 'description',
          isAllDay: false,
          duration: durationToSave,
          durationCurrent: durationToSave,
          fixed: fixed,
          type: 'live',
        };
        addProgram(slot);

        if (recurrenceParameters.reccurencePatternId === 3) {
          if (daysIndex === recurrenceParameters.days.length - 1) {
            daysIndex = 0;
            dateNextWeek = getDateNextWeek(dateNextWeek);
          } else daysIndex = daysIndex + 1;
        }
      }
      setRecurrenceParameters({});
    }
  };

  const getDateWithoutTime = (date) => {
    return new Date(
      date.getFullYear(),
      date.getMonth(),
      date.getDate(),
      0,
      0,
      0,
      0
    );
  };

  const handleSaveForm = () => {
    setValidation(SchedulerStorage.addSlotValidation);
    let hasError = false;

    const durationSplit = durationValue.split(':');
    if (live) {
      if (!liveNameValue) {
        setValidation((prevState) => ({
          ...prevState,
          errorLiveName: true,
        }));
        hasError = true;
      }
      if (!isRegularSlot) {
        if (!numberOfSlotsValue || isNaN(numberOfSlotsValue)) {
          setValidation((prevState) => ({
            ...prevState,
            errorNumberOfSlots: true,
          }));
          hasError = true;
        } else if (
          parseInt(numberOfSlotsValue) < 1 ||
          parseInt(numberOfSlotsValue) > 999
        ) {
          setValidation((prevState) => ({
            ...prevState,
            errorNumberOfSlots: true,
          }));
          hasError = true;
        }
      }
    } else {
      if (selectedTab === 'Titles') {
        if (
          selectedProgram &&
          Object.keys(selectedProgram).length === 0 &&
          Object.getPrototypeOf(selectedProgram) === Object.prototype
        ) {
          setValidation((prevState) => ({
            ...prevState,
            errorTitle: true,
          }));
          hasError = true;
        } else {
          const durationInput =
            parseInt(durationSplit[0]) * 60 + parseInt(durationSplit[1]);
          const durationTitleSplit = selectedProgram.duration.split(':');
          const durationTitle =
            parseInt(durationTitleSplit[0]) * 60 +
            parseInt(durationTitleSplit[1]);
          if (durationInput < durationTitle) {
            setValidation((prevState) => ({
              ...prevState,
              errorSelectedDuration: true,
            }));
            hasError = true;
          }
        }
      } else {
        if (
          (!numberOfSlotsValue || isNaN(numberOfSlotsValue)) &&
          activeTab !== 'Title Groups'
        ) {
          setValidation((prevState) => ({
            ...prevState,
            errorNumberOfSlots: true,
          }));
          hasError = true;
        } else if (
          parseInt(numberOfSlotsValue) < 1 ||
          parseInt(numberOfSlotsValue) > 999
        ) {
          setValidation((prevState) => ({
            ...prevState,
            errorNumberOfSlots: true,
          }));
          hasError = true;
        }
        if (selectedProgramGroup.length === 0) {
          setValidation((prevState) => ({
            ...prevState,
            errorTitle: true,
          }));
          hasError = true;
        }
      }
    }

    if (!timeValue) {
      setValidation((prevState) => ({
        ...prevState,
        errorNullTime: true,
      }));
      hasError = true;
    } else if (
      timeValue.getHours() < 6 &&
      dateRange.start.getFullYear() === dateValue.getFullYear() &&
      dateRange.start.getDate() === dateValue.getDate() &&
      dateRange.start.getMonth() === dateValue.getMonth()
    ) {
      setValidation((prevState) => ({
        ...prevState,
        receedTime: true,
      }));
      hasError = true;
    }

    if (
      startDateRef.current.children.day.value === '00' ||
      startDateRef.current.children.month.value === '00'
    ) {
      setValidation((prevState) => ({
        ...prevState,
        invalidDate: true,
      }));
      hasError = true;
    } else if (!dateValue) {
      setValidation((prevState) => ({
        ...prevState,
        errorNullDate: true,
      }));
      hasError = true;
    } else if (dateValue >= dateRange.end) {
      setValidation((prevState) => ({
        ...prevState,
        errorDateTime: true,
      }));
      hasError = true;
    } else if (
      getDateWithoutTime(dateValue) < getDateWithoutTime(dateRange.start)
    ) {
      setValidation((prevState) => ({
        ...prevState,
        receedDate: true,
      }));
      hasError = true;
    }
    if (parseInt(durationSplit[0]) > 99 || parseInt(durationSplit[1]) > 99) {
      setValidation((prevState) => ({
        ...prevState,
        errorDuration: true,
      }));
      hasError = true;
    }
    if (
      (!durationValue || !durationSplit[0] || !durationSplit[1]) &&
      (selectedTab !== 'Title Groups' || (!isRegularSlot && live))
    ) {
      setValidation((prevState) => ({
        ...prevState,
        errorDuration: true,
      }));
      hasError = true;
    }
    if (
      (selectedTab === 'Title Groups' || live) &&
      durationValue &&
      durationSplit.length < 2
    ) {
      setValidation((prevState) => ({
        ...prevState,
        errorDuration: true,
      }));
      hasError = true;
    }

    if (!hasError) {
      if (live) {
        addLiveSlot(durationSplit);
      } else if (isRegularSlot) {
        addRegularSlot(durationSplit);
      } else {
        addSeriesSlot(durationSplit);
      }
    }
  };

  const onChangeDate = (value) => {
    setDateValue(value);
    setValidation((prevState) => ({
      ...prevState,
      errorNullDate: false,
      errorDateTime: false,
      invalidDate: false,
      receedDate: false,
    }));
  };

  const onChangeTime = (value) => {
    setTimeValue(value);
    setValidation((prevState) => ({
      ...prevState,
      errorNullTime: false,
      receedTime: false,
    }));
  };

  const clearModal = () => {
    setDateTimeValue(parseAdjust(dateRange.start));
    setSelectedProgram({});
    setSelectedProgramGroup([]);
  };

  const clearValidation = () => {
    setValidation({
      errorNullDate: false,
      errorDateTime: false,
      errorTitle: false,
    });
  };

  const onChangeDuration = (e) => {
    setDurationValue(e.target.value);
    if (
      !(
        selectedProgram &&
        Object.keys(selectedProgram).length === 0 &&
        Object.getPrototypeOf(selectedProgram) === Object.prototype
      )
    ) {
      const durationSplit = e.target.value.split(':');
      const durationInput =
        parseInt(durationSplit[0]) * 60 + parseInt(durationSplit[1]);
      const durationTitleSplit = selectedProgram.duration.split(':');
      const durationTitle =
        parseInt(durationTitleSplit[0]) * 60 + parseInt(durationTitleSplit[1]);
      if (durationInput < durationTitle) {
        setValidation((prevState) => ({
          ...prevState,
          errorSelectedDuration: true,
        }));
      } else {
        setValidation((prevState) => ({
          ...prevState,
          errorSelectedDuration: false,
        }));
      }
    } else {
      setValidation((prevState) => ({
        ...prevState,
        errorSelectedDuration: false,
      }));
    }
  };

  const onKeyPressDuration = (e) => {
    var key = e.key;
    if (isNaN(parseInt(key)) && key !== ':' && key !== 'Backspace')
      e.preventDefault();
  };

  const onChangeNumberOfSlots = (e) => {
    setNumberOfSlotsValue(e.target.value);
  };

  const onKeyPressNumberOfSlots = (e) => {
    var key = e.key;
    if (isNaN(parseInt(key)) && key !== 'Backspace') {
      e.preventDefault();
    }
    if (e.keyCode == 65 && e.ctrlKey) {
      e.target.select();
    }
  };

  const handleOnChangeRecurrence = (data) => {
    setRecurrenceParameters(data);
  };

  useEffect(() => {
    if (!showModal) {
      clearModal();
    } else {
      if (formType !== 'A') {
        const startTime = DateService.addHours(
          selectedSlot.start,
          clockStart * -1
        );
        if (parseAdjust(startTime).getDate() !== selectedSlot.start.getDate()) {
          setDateTimeValue(parseAdjust(DateService.addHours(startTime, 24)));
        } else {
          setDateTimeValue(parseAdjust(startTime));
        }
      } else {
        if (defaultAddDate) {
          var dateDefault = parseAdjust(defaultAddDate);
          dateDefault.setHours(dateDefault.getHours() + 6);
          dateDefault.setMinutes(dateDefault.getMinutes());
          dateDefault.setMilliseconds(0);
          setDateValue(dateDefault);
          setTimeValue(dateDefault);
        } else {
          var dateDefault = parseAdjust(dateRange.start);
          dateDefault.setHours(6);
          dateDefault.setMinutes(0);
          dateDefault.setMilliseconds(0);
          setDateValue(dateDefault);
          setTimeValue(dateDefault);
        }
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [showModal]);

  useEffect(() => {
    if (dateValue && timeValue) {
      const dateTime = dateValue;
      dateValue.setHours(timeValue.getHours());
      dateValue.setMinutes(timeValue.getMinutes());
      setDateTimeValue(dateTime);
    }
  }, [dateValue, timeValue]);

  useEffect(() => {
    if (
      !(
        selectedProgram && //  null and undefined check
        Object.keys(selectedProgram).length === 0 &&
        Object.getPrototypeOf(selectedProgram) === Object.prototype
      )
    ) {
      durationRef.current.value = selectedProgram.duration.slice(0, 5);
      setDurationValue(selectedProgram.duration.slice(0, 5));
    }
  }, [selectedProgram]);

  useEffect(() => {
    if (durationRef.current) {
      durationRef.current.value = '';
    }
    if (numberSlotRef.current) {
      numberSlotRef.current.value = 1;
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const [tabs, setTabs] = useState([
    {
      id: 1,
      label: 'Titles',
      component: (
        <SelectProgramsTableComponent
          selectProgram={setSelectedProgram}
          type={'A'}
          clearValidation={clearValidation}
          height={SchedulerStorage.addTitlesTableHeight}
          activeTab={activeTab}
          setHasTitles={(e) => setHasTitles(e)}
        />
      ),
    },
    {
      id: 2,
      label: 'Title Groups',
      component: (
        <SelectTitleGroupTableComponent
          selectProgram={setSelectedProgram}
          type={'A'}
          clearValidation={clearValidation}
          height={tableHeights}
          activeTab={activeTab}
          setHasTitles={(e) => setHasTitles(e)}
        />
      ),
    },
  ]);
  useEffect(() => {
    if (isRegularSlot) {
      setActiveTab('Titles');
      setTabs([
        {
          id: 1,
          label: 'Titles',
          component: (
            <SelectProgramsTableComponent
              selectProgram={setSelectedProgram}
              type={'A'}
              clearValidation={clearValidation}
              height={SchedulerStorage.addTitlesTableHeight}
              activeTab={'Titles'}
              setHasTitles={(e) => setHasTitles(e)}
            />
          ),
        },
        {
          id: 2,
          label: 'Title Groups',
          component: (
            <SelectTitleGroupTableComponent
              selectProgram={setSelectedProgramGroup}
              type={'A'}
              clearValidation={clearValidation}
              height={tableHeights}
              activeTab={activeTab}
              setHasTitles={(e) => setHasTitles(e)}
            />
          ),
        },
      ]);
    } else {
      setActiveTab('Title Groups');
      setTabs([
        {
          id: 2,
          label: 'Title Groups',
          component: (
            <SelectTitleGroupTableComponent
              selectProgram={(data) => {
                setNumberOfSlotsValue(data.length);
                setSelectedProgramGroup(data);
              }}
              type={'A'}
              clearValidation={clearValidation}
              height={tableHeights}
              activeTab={'Title Groups'}
              setHasTitles={(e) => setHasTitles(e)}
            />
          ),
        },
      ]);
    }
    if (!isRegularSlot && numberSlotRef.current) {
      numberSlotRef.current.value = 1;
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isRegularSlot]);

  useEffect(() => {
    setValidation(SchedulerStorage.addSlotValidation);

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [activeTab, selectedProgram]);

  useEffect(() => {
    if (
      !(
        selectedProgram &&
        Object.keys(selectedProgram).length === 0 &&
        Object.getPrototypeOf(selectedProgram) === Object.prototype
      )
    )
      setSelectedProgram({});
    if (selectedProgramGroup.length > 0) setSelectedProgramGroup([]);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [activeTab]);

  return (
    <Dialog closeIcon={null} title={headerText} onClose={closeModal}>
      <div className={Styles.contentWrapper}>
        <div className="pb-2">
          <strong>Slot Type</strong>
        </div>
        <div className={`d-flex col-12 pb-2 pl-2 ${Styles.formPaddingLeft}`}>
          <div
            className={`form-check ${Styles.slotTypeWidth} ${Styles.formPaddingTopRadio}`}
          >
            <input
              className={`form-check-input ${Styles.formCheckMargin}`}
              type="radio"
              name="radioRegular"
              checked={isRegularSlot}
              onClick={() => setIsRegularSlot(true)}
            />
            <label class="form-check-label" for="flexRadioDefault1">
              Regular
            </label>
          </div>

          <div
            className={`form-check ${Styles.slotTypeWidth} ${Styles.formPaddingTopRadio}`}
          >
            <input
              class={`form-check-input ${Styles.formCheckMargin}`}
              type="radio"
              name="radioSeries"
              checked={!isRegularSlot}
              onClick={() => setIsRegularSlot(false)}
            />
            <label class="form-check-label" for="flexRadioDefault1">
              Series
            </label>
          </div>
          <div className={`${Styles.slotTypeWidth}`}>
            <SwitchComponent
              label={'Fixed'}
              text={['Yes', 'No']}
              active={fixed}
              onSwitch={() => setFixed(!fixed)}
              name={'Fixed'}
              isModal={true}
            />
          </div>

          <div className={`${Styles.slotTypeWidth}`}>
            <SwitchComponent
              label={'Live'}
              text={['Yes', 'No']}
              active={live}
              onSwitch={() => setLive(!live)}
              name={'Live'}
              isModal={true}
            />
          </div>
        </div>
        <div className="d-flex">
          <div
            className={`${Styles.formMarginRight} ${
              isRegularSlot ? Styles.dateTimeWidthLive : Styles.dateTimeWidth
            }`}
          >
            <div class="form-group">
              <label>Start date</label>
              <CustomDateInput
                ref={startDateRef}
                value={dateValue}
                max={dateRange.end}
                min={dateRange.start}
                onChange={onChangeDate}
                disabled={!(formType === 'A')}
              />
              {validation.errorNullDate && (
                <small class="errorValidation">Start date is required</small>
              )}
              {validation.invalidDate && (
                <small class="errorValidation">Start date is invalid</small>
              )}
              {validation.errorDateTime && (
                <small class="errorValidation">Exceeded date period</small>
              )}
              {validation.receedDate && (
                <small class="errorValidation">
                  Start date should not be earlier than period range
                </small>
              )}
            </div>
          </div>

          <div
            className={`${Styles.formMarginRight} ${Styles.startTimeInput} ${
              isRegularSlot ? Styles.dateTimeWidthLive : Styles.dateTimeWidth
            }`}
          >
            <div class="form-group">
              <label>Start time</label>
              <CustomTimeInput
                value={timeValue}
                disabled={!(formType === 'A')}
                onChange={onChangeTime}
              />
              {validation.errorNullTime && (
                <small class="errorValidation">Start time is required</small>
              )}
              {validation.receedTime && (
                <small class="errorValidation">
                  Start time should not be earlier than 6:00
                </small>
              )}
            </div>
          </div>

          <div
            className={`${Styles.formMarginRight} ${
              isRegularSlot ? Styles.dateTimeWidthLive : Styles.dateTimeWidth
            }`}
          >
            <div class="form-group">
              <label>Duration</label>
              <input
                ref={durationRef}
                className={`${Styles.durationInput}`}
                type="text"
                placeholder=""
                onChange={onChangeDuration}
                onKeyPress={onKeyPressDuration}
              />
              {validation.errorDuration && (
                <small class="errorValidation">
                  Please provide the duration in hh:mm.
                </small>
              )}
              {validation.errorSelectedDuration && (
                <small class="errorValidation">
                  Duration cannot be less than programme title
                </small>
              )}
            </div>
          </div>
          {!isRegularSlot && (
            <div className={`m-1 ${Styles.dateTimeWidth}`}>
              <div class="form-group">
                <label>Number of Slots</label>
                <input
                  ref={numberSlotRef}
                  className={`${Styles.durationInput}`}
                  type="text"
                  value={numberOfSlotsValue}
                  placeholder="Min: 1, Max: 999"
                  onKeyDown={onKeyPressNumberOfSlots}
                  onChange={onChangeNumberOfSlots}
                />
                {validation.errorNumberOfSlots && (
                  <small class="errorValidation">Invalid Number of slots</small>
                )}
              </div>
            </div>
          )}
        </div>
        {!live ? (
          <div className={`${Styles.divTabsMargin}`}>
            <TabsComponent
              activeTab={activeTab}
              labelList={tabs}
              customClass={` ${
                activeTab == 'Titles'
                  ? Styles.tabsSchedule
                  : Styles.tabsScheduleGroups
              }`}
              setActive={(e) => setActiveTab(e)}
              onSetSelectedTab={setSelectedTab}
              customClassActiveHeader={`${Styles.tabsScheduleHeaderActive}`}
            />
          </div>
        ) : (
          <LiveComponent
            regularSlot={isRegularSlot}
            validation={validation}
            liveNameValue={liveNameValue}
            setLiveNameValue={setLiveNameValue}
          />
        )}

        {validation.errorTitle && (
          <small class="errorValidation">Please select a title</small>
        )}

        {((isRegularSlot && selectedTab === 'Title Groups' && !live) ||
          (!isRegularSlot && (selectedTab !== 'Series' || live))) && (
          <div>
            <ReccurencePatternComponent
              isRegular={isRegularSlot}
              numberOfSlots={selectedProgramGroup.length}
              onChangeRecurrence={handleOnChangeRecurrence}
            />
          </div>
        )}
      </div>

      <div className={Styles.divFooterModal}>
        <button className={Styles.cancelButton} onClick={closeModal}>
          Cancel
        </button>
        <button
          className={Styles.saveButton}
          onClick={handleSaveForm}
          disabled={
            (!isRegularSlot && !live && selectedTab === 'Series') ||
            (recurrenceParameters.reccurencePatternId === 2 &&
              recurrenceParameters.intervalId === 1 &&
              recurrenceParameters.intervalValue < 1) ||
            (recurrenceParameters.reccurencePatternId === 3 &&
              recurrenceParameters.everyWeekInterval < 1) ||
            (!hasTitles && activeTab == 'Titles' && !live) ||
            (!hasTitles && activeTab == 'Title Groups' && !live) ||
            (!isRegularSlot &&
              activeTab === 'Title Groups' &&
              numberOfSlotsValue === '0')
          }
        >
          Insert
        </button>
      </div>
    </Dialog>
  );
};
