import React, { useRef } from 'react';
import { Modal } from 'react-bootstrap';
import scheduleImportTemplate from '../../../../../assets/files/schedule-import-template.xlsx';
import styles from './ImportSlots.module.css';
import schedulerStyles from '../../ReactSchedule.module.css';
import { saveAs } from 'file-saver';
export default function ImportFileModalComponent({
  closeModal,
  selectedFile,
  setSelectedFile,
  handleOnImportFile,
  errorMessage,
}) {
  const chooseFileRef = useRef();

  const handleUploadButton = () => {
    chooseFileRef.current.click();
  };

  const onFileChangeCapture = (e) => {
    const file = e.target.files[0];
    setSelectedFile(file);
  };

  const exportSampleTemplate = () => {
    saveAs(scheduleImportTemplate, 'schedule-import-template.xlsx');
  };

  return (
    <Modal show={true} centered size="lg">
      <div className={styles.body}>
        <div>
          <div style={{ fontWeight: 'bolder' }}>Import schedule</div>
          <p className={styles.smallText}>
            Download a{' '}
            <span
              style={{
                color: '#008662',
                cursor: 'pointer',
              }}
              onClick={exportSampleTemplate}
              onKeyDown={exportSampleTemplate}
              role="presentation"
            >
              sample template{' '}
            </span>
            to see an example of the required format.
          </p>
          <p
            style={{
              marginTop: '-15px',
              color: 'red',
              fontSize: '14px',
            }}
          >
            {errorMessage}
          </p>

          <div className="d-flex">
            <button
              className={`${schedulerStyles.saveButton} ${styles.marginTop30}`}
              onClick={handleUploadButton}
            >
              Choose file
            </button>
            <input
              ref={chooseFileRef}
              hidden
              type="file"
              accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel, .csv"
              onChange={onFileChangeCapture}
            />

            <p
              className={styles.fileName}
              title={selectedFile && selectedFile.name}
            >
              {selectedFile && selectedFile.name}
            </p>
          </div>

          <div
            style={{
              paddingTop: '10px',
            }}
          >
            <small>Acceptable file formats: Excel, CSV</small>
          </div>
        </div>

        <div className={styles.buttons}>
          <button className={styles.cancelButton} onClick={closeModal}>
            Cancel
          </button>
          <button
            className={schedulerStyles.saveButton}
            onClick={handleOnImportFile}
            disabled={!selectedFile}
          >
            Import
          </button>
        </div>
      </div>
    </Modal>
  );
}
