import React, { useEffect, useState } from 'react';
import {
  Scheduler,
  WeekView,
  SchedulerHeader,
  WorkWeekView,
} from '@progress/kendo-react-scheduler';
import '@progress/kendo-theme-default/dist/all.css';
import { IntlProvider, IntlService } from '@progress/kendo-react-intl';
import '../kendo-scheduler.css';
import { Day } from '@progress/kendo-date-math';
import DateService from '../../../../core/date.service';
import { AddProgramsComponent } from '../forms/AddProgramsComponent';
let dateRangeValue = { start: new Date(), end: new Date() };
class CustomFormatIntlService extends IntlService {
  formatDate(value, format) {
    switch (format.skeleton || format) {
      case 'MEd':
        if (
          new Date(value._utcDate).setHours(0, 0, 0, 0) <
            new Date(dateRangeValue.start).setHours(0, 0, 0, 0) ||
          new Date(value._utcDate).setHours(0, 0, 0, 0) >
            new Date(dateRangeValue.end).setHours(0, 0, 0, 0)
        )
          return '';
        return super.formatDate(value, 'E dd/MM/y');
      case 't':
        const time = super.formatDate(value, 'H:mm').split(':');
        let hr = parseInt(time[0]) + 6;
        const timeFormated = hr + ':' + time[1];
        return timeFormated;
      default:
        return super.formatDate(value, format);
    }
  }
}

class CustomIntlProvider extends IntlProvider {
  getIntlService() {
    return new CustomFormatIntlService(this.props.locale);
  }
}

function StandardTimeComponent({
  tableHeight,
  sampleData,
  displayDate,
  handleDateChange,
  CustomViewSlot,
  handleDataChange,
  scheduleHeader,
  CustomItem,
  dateRange,
  addProgram,
  parseAdjust,
  clockStart,
}) {
  const [weekStart, setWeekStart] = useState(Day.Sunday);
  const [weekEnd, setWeekEnd] = useState(Day.Saturday);
  const [showAddModal, setShowAddModal] = useState(false);
  const [defaultDate, setDefaultDate] = useState(new Date());
  const onChangeDate = () => {
    const dayOfWeek = displayDate.getDay();
    const currentDate = new Date(displayDate);
    const lastWeekDate = new Date(
      DateService.addDays(currentDate, 6 - dayOfWeek)
    );
    if (new Date(dateRange.start).getTime() == new Date(displayDate).getTime())
      setWeekStart(displayDate.getDay());
    else setWeekStart(Day.Sunday);
    if (new Date(dateRange.end).getTime() < lastWeekDate.getTime())
      setWeekEnd(dateRange.end.getDay());
    else setWeekEnd(Day.Saturday);
  };

  useEffect(() => {
    dateRangeValue = dateRange;
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dateRange]);

  const customForm = (props) => {
    useEffect(() => {
      let start = new Date(
        props.dataItem.start.getFullYear(),
        props.dataItem.start.getMonth(),
        props.dataItem.start.getDate(),
        props.dataItem.start.getHours() + 6,
        props.dataItem.start.getMinutes()
      );
      if (dateRange.start <= start && dateRange.end >= start) {
        setDefaultDate(props.dataItem.start);
        setShowAddModal(true);
      }
      props.onClose();
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [props]);
    return null;
  };

  return (
    <>
      <CustomIntlProvider locale="en">
        <Scheduler
          height={tableHeight}
          data={sampleData}
          date={displayDate}
          onDateChange={handleDateChange}
          viewSlot={CustomViewSlot}
          onDataChange={handleDataChange}
          header={(props) => (
            <SchedulerHeader {...props} children={scheduleHeader()} />
          )}
          footer={(props) => <></>}
          editable={{
            add: true,
            remove: false,
            drag: true,
            resize: false,
            edit: false,
          }}
          form={customForm}
        >
          <WorkWeekView
            slotDuration={60}
            slotDivisions={12}
            item={CustomItem}
            startTime={'7:00'}
            endTime={'19:00'}
            workDayStart={'0:00'}
            workDayEnd={'23:55'}
            workWeekStart={weekStart}
            workWeekEnd={weekEnd}
          />
        </Scheduler>
      </CustomIntlProvider>
      {showAddModal && (
        <AddProgramsComponent
          showModal={showAddModal}
          closeModal={() => setShowAddModal(false)}
          addProgram={(e) => {
            addProgram(e);
            setShowAddModal(false);
          }}
          parseAdjust={parseAdjust}
          formType={'A'}
          headerText={'New Slot'}
          clockStart={clockStart}
          displayDate={displayDate}
          defaultAddDate={defaultDate}
          dateRange={dateRange}
        />
      )}
    </>
  );
}

export default StandardTimeComponent;
