import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

import styles from './styles.module.css';

function SwitchComponent({
  label,
  text,
  active,
  disabled,
  onSwitch,
  customWidth,
  right,
  name,
  blurredOnInactive,
  isModal,
}) {
  const [isActiveSwitch, setIsActiveSwitch] = useState(active);

  useEffect(() => {
    setIsActiveSwitch(active);
  }, [active]);

  const executeOnSwitch = () => {
    if (!disabled) {
      // typeof onSwitch === 'function' && onSwitch(!isActiveSwitch, name);
      if (typeof onSwitch === 'function') onSwitch(!isActiveSwitch, name);
      setIsActiveSwitch(!isActiveSwitch);
    }
  };

  return (
    <div className={styles.switchDiv}>
      {!right && (
        <div
          className={`${styles.label} ${blurredOnInactive &&
            styles.disabledLabel}`}
        >
          {label}
        </div>
      )}
      {text ? (
        <div>
          {isActiveSwitch ? (
            <div
              className={`${styles.activeSwitch} ${disabled &&
                (isActiveSwitch
                  ? styles.disabledActiveSwitch
                  : styles.disabledSwitch)} ${
                styles.activeSwitchWithText
              } ${isModal && styles.switchModal}`}
              onClick={executeOnSwitch}
              style={{ width: customWidth || null }}
              onKeyDown={executeOnSwitch}
              role="presentation"
            >
              <span
                className={`${styles.activeText} ${isModal &&
                  styles.isModalText}`}
              >
                {text && text[0]}
              </span>
            </div>
          ) : (
            <div
              className={`${styles.switch} ${disabled &&
                (isActiveSwitch
                  ? styles.disabledActiveSwitch
                  : styles.disabledSwitch)} ${
                styles.switchWithText
              } ${isModal && styles.switchModal}`}
              onClick={executeOnSwitch}
              style={{ width: customWidth || null }}
              onKeyDown={executeOnSwitch}
              role="presentation"
            >
              <span
                className={`${styles.text} ${isModal && styles.isModalText}`}
              >
                {text[1]}
              </span>
            </div>
          )}
        </div>
      ) : (
        <div
          className={`${
            isActiveSwitch ? styles.activeSwitch : styles.switch
          } ${disabled &&
            (isActiveSwitch
              ? styles.disabledActiveSwitch
              : styles.disabledSwitch)}`}
          onClick={executeOnSwitch}
          onKeyDown={executeOnSwitch}
          role="presentation"
        />
      )}

      {right && (
        <div
          className={`${styles.label} ${blurredOnInactive &&
            styles.disabledLabel}`}
          role="presentation"
        >
          {label}
        </div>
      )}
    </div>
  );
}

SwitchComponent.propTypes = {
  label: PropTypes.string,
  text: PropTypes.arrayOf(PropTypes.string).isRequired,
  disabled: PropTypes.bool,
  onSwitch: PropTypes.func.isRequired,
  customWidth: PropTypes.string,
  right: PropTypes.bool,
  name: PropTypes.string,
  active: PropTypes.bool.isRequired,
  isModal: PropTypes.bool,
};

SwitchComponent.defaultProps = {
  label: '',
  text: '',
  disabled: false,
  customWidth: '',
  right: true,
  name: '',
  isModal: false,
};

export default React.memo(SwitchComponent);
