import React, { useState, useEffect } from 'react';
import Table from '../../../../shared/components/table/Table';
import SchedulerStorage from '../SchedulerStorage';
import ScheduleService from '../../ScheduleService';
import AuthService from '../../../../core/auth.service';
import { eyeIcon, eyeSlashIcon } from '../../../../assets/index';
import Styles from './ScheduleForms.module.css';
import PrioritisationService from '../../../prioritisation/PrioritisationService';
import ProgramDetailsComponent from '../../../prioritisation/prioritisation-program-details/PrioritisationProgramDetailComponent';

function SelectProgramsTableComponent(props) {
  const tableHeaders = SchedulerStorage.tableHeaders;
  const [isLoading, setIsLoading] = useState(true);
  const [isExporting, setIsExporting] = useState(false);
  const [selectedData, setSelectedData] = useState();
  const [data, setData] = useState([]);
  const [queryParameters, setQueryParameters] = useState({});
  const [tableParameters, setTableParameters] = useState({});
  const [paginationParameters, setPaginationParameters] = useState({});
  const [sortParameters, setSortParameters] = useState({ order: 1, sort: 1 });
  const [headerData, setHeaderData] = useState({});
  const [programDetails, setProgramDetails] = useState({});
  const [currentTitle, setCurrentTitle] = useState(null);
  const [component, setComponent] = useState('');

  const constructQueryParameters = (parameters) => {
    const pagination = `?pageNumber=${
      parameters.pageNumber ? parameters.pageNumber : 1
    }
    &pageSize=${parameters.pageSize ? parameters.pageSize : 50}`;

    const title =
      parameters.titleFilter && parameters.titleFilterValue
        ? `&titleFilter=${parameters.titleFilter}&titleFilterValue=${parameters.titleFilterValue}`
        : '';

    const genre =
      parameters.genreFilter && parameters.genreFilterValue
        ? `&genreFilter=${parameters.genreFilter}&genreFilterValue=${parameters.genreFilterValue}`
        : '';

    const duration =
      parameters.durationFilter && parameters.durationFilterValue
        ? `&durationFilter=${parameters.durationFilter}&durationFilterValue=${parameters.durationFilterValue}`
        : '';

    const grade =
      parameters.gradeFilter && parameters.gradeFilterValue
        ? `&gradeFilter=${parameters.gradeFilter}&gradeFilterValue=${parameters.gradeFilterValue}`
        : '';

    const certificate =
      parameters.certificateFilter && parameters.certificateFilterValue
        ? `&certificateFilter=${parameters.certificateFilter}&certificateFilterValue=${parameters.certificateFilterValue}`
        : '';

    let year =
      parameters.yearFilter && parameters.yearFilterValue
        ? `&yearFilter=${parameters.yearFilter}&yearFilterValue=${parameters.yearFilterValue}`
        : '';

    const sort =
      parameters.sortOrder && parameters.sortBy
        ? `&sort=${parameters.sortBy}&sortBy=${parameters.sortOrder}`
        : '';

    let searchKey = parameters.searchKey
      ? `&SearchKey=${parameters.searchKey}`
      : '';
    if (parameters.searchKey != null && parameters.searchKey.includes('+')) {
      searchKey = searchKey.replace('+', '%2B');
    }
    const useCache = parameters.useCache
      ? `&useCache=${parameters.useCache}`
      : '';

    let queryString =
      pagination +
      title +
      genre +
      duration +
      grade +
      certificate +
      year +
      sort +
      searchKey +
      useCache;

    if (queryString.includes('+')) {
      queryString = queryString.replace('+', '%2B');
    }

    return queryString;
  };

  const viewTitleDetails = (selectedId) => {
    PrioritisationService.getTitleByID(selectedId, headerData.filter.channelId).then(
      (response) => {
        if (response.data.titleId === programDetails.titleId) {
          setProgramDetails({});
          setData((old) =>
            old.map((item) => ({
              ...item,
              action: { ...item.action, icon: eyeIcon },
            }))
          );
        } else {
          setProgramDetails(response.data);
          setData((old) =>
            old.map((item) =>
              item.id === selectedId
                ? {
                    ...item,
                    action: { ...item.action, icon: eyeSlashIcon },
                  }
                : {
                    ...item,
                    action: { ...item.action, icon: eyeIcon },
                  }
            )
          );
        }
      }
    );
  };

  const handleOnAction = (actionName, selectedId) => {
    props.clearValidation();
    viewTitleDetails(selectedId);
  };

  const handleGetTitles = () => {
    setIsLoading(true);
    var titleId = currentTitle == null ? 0 : currentTitle.id;
    const period = headerData.period;
    ScheduleService.getSchedulePrograms(
      constructQueryParameters(queryParameters),
      headerData.filter,
      period,
      titleId
    )
      .then((response) => {
        const reponseTitles = response.data.data;
        const titles = reponseTitles.map((title) => ({
          id: title.id,
          titleName: title.titleName,
          genre: title.genre,
          duration: title.duration,
          grade: title.grade,
          certificate: title.certificate,
          year: title.year,
          hasSegment: `hidden:${title.hasSegment}`,
          windowStartDateString: `hidden:${title.windowStartDateString}`,
          windowEndDateString: `hidden:${title.windowEndDateString}`,
          action: {
            id: title.id,
            icon: eyeIcon,
            toolTip: 'View title details',
            actionName: 'viewTitleDetails',
          },
        }));
        setData(titles);
        setPaginationParameters({
          totalCount: response.data.totalData,
          pageSize: response.data.size,
          currentPage: response.data.page,
          totalPages: response.data.totalPages,
        });
        props.setHasTitles && props.setHasTitles(response.data.totalData > 0);
      })
      .finally(() => setIsLoading(false));
  };

  const handleOnFilter = (selectedData) => {
    setQueryParameters({
      ...queryParameters,
      ...selectedData,
      pageNumber: 1,
      pageSize: queryParameters.pageSize,
      searchKey: queryParameters.searchKey,
    });
    setTableParameters({
      ...queryParameters,
      ...selectedData,
      pageNumber: 1,
      pageSize: queryParameters.pageSize,
      searchKey: queryParameters.searchKey,
    });
  };

  const handleOnClearFilter = () => {
    setQueryParameters({
      pageNumber: 1,
      pageSize: queryParameters.pageSize,
      searchKey: queryParameters.searchKey,
    });
    setTableParameters({});
  };

  const handleOnSearch = (searchKey) => {
    setQueryParameters({
      ...queryParameters,
      pageNumber: 1,
      pageSize: queryParameters.pageSize,
      searchKey,
    });
  };

  const handleOnSort = ({ sortBy, sortOrder }) => {
    setSortParameters({ order: sortOrder, sort: sortBy });
    setQueryParameters({
      ...queryParameters,
      pageNumber: 1,
      pageSize: queryParameters.pageSize,
      sortBy,
      sortOrder,
    });
  };

  const handleOnPaginate = (pageNumber, pageSize) => {
    setQueryParameters({ ...queryParameters, pageNumber, pageSize });
  };

  const handleSelectData = (selectedData) => {
    setSelectedData(selectedData);
  };

  useEffect(() => {
    if (props.component !== '') {
      setComponent(props.component);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.component]);

  useEffect(() => {
    if (Object.keys(headerData).length > 0) {
      handleGetTitles();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [queryParameters, headerData]);

  useEffect(() => {
    setHeaderData(AuthService.getSelectedSchedule());
    setCurrentTitle(props.value);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    setCurrentTitle(props.value);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.value]);

  const handleChange = (event) => {
    setSelectedData(event);
    if (props.type == 'A') {
      props.selectProgram(event);
    } else if (props.type === 'E') {
      if (props.selectProgram) {
        props.selectProgram(event);
      }
    } else {
      if (props.onChange) {
        props.onChange.call(undefined, {
          value: event,
        });
      }
    }
  };

  const callbackCloseDetails = () => {
    setProgramDetails({});
    setData((old) =>
      old.map((item) => ({
        ...item,
        action: { ...item.action, icon: eyeIcon },
      }))
    );
  };

  return (
    <>
      <div class="col-12" style={{ flexWrap: 'wrap' }}>
        <div className="pb-2 col-12">Available Program Titles</div>
        <div style={{ width: '100%' }}>
          <Table
            noDataVerbiage="No available programme titles in this channel covering the schedule's period."
            headers={tableHeaders}
            list={data}
            isExporting={isExporting}
            isFetchingData={isLoading}
            queryParameters={tableParameters}
            paginationParameters={paginationParameters}
            sortParameters={sortParameters}
            onFilter={handleOnFilter}
            onClearFilters={handleOnClearFilter}
            onSort={handleOnSort}
            onPaginate={handleOnPaginate}
            onDoubleClickSelectData={handleSelectData}
            onSingleClickSelectData={handleChange}
            tableHeight={props.height === null ? '500px' : `${Object.keys(programDetails).length > 0 ? props.height + 200 : props.height}px`}
            filter={null}
            onAction={handleOnAction}
            defaultSelected={currentTitle}
            rowStyle={Styles.rowTitlesTable}
          />
        </div>
        {Object.keys(programDetails).length > 0 && (
          <div className={`${Styles.programDetailsHeight} mt-3`}>
            <ProgramDetailsComponent
              component="Schedule"
              program={programDetails}
              callbackCloseDetails={callbackCloseDetails}
            />
          </div>
        )}
      </div>
    </>
  );
}

export default SelectProgramsTableComponent;
