import React, { useState } from 'react';
import PropTypes from 'prop-types';

import styles from './PrioritisationProgramDetails.module.css';
import closeIcon from '../../../assets/icons/Close.svg';

import {
  attributesOverrideGreenIcon,
  attributesOverrideGreenToggledIcon,
} from '../../../assets';
import { useEffect } from 'react';

function ProgramDetailsComponent({ component, program, callbackCloseDetails }) {
  const [overrideValues, setOverrideValues] = useState({
    title: false,
    genre: false,
    grade: false,
    certificate: false,
    yearofProduction: false,
    duration: false,
  });
  const overrideSeparator = ':@:';

  const handleToggleOverrideValues = (name) => {
    setOverrideValues({ ...overrideValues, [name]: !overrideValues[name] });
  };

  useEffect(() => {
    if (program) {
      setOverrideValues({
        title: false,
        genre: !program.titleAttributes.Genre
          ? ''
          : program.titleAttributes.Genre.split(overrideSeparator)[1],
        grade: !program.titleAttributes.Grade
          ? ''
          : program.titleAttributes.Grade.split(overrideSeparator)[1],
        certificate: !program.titleAttributes.Certificate
          ? ''
          : program.titleAttributes.Certificate.split(overrideSeparator)[1],
        yearofProduction: program.titleAttributes.YearofProduction
          ? program.titleAttributes.YearofProduction.split(overrideSeparator)[1]
          : '',
        duration: !program.titleAttributes.Duration
          ? ''
          : program.titleAttributes.Duration.split(overrideSeparator)[1],
      });
    }
  }, [program]);

  return (
    <div>
      {program && (
        <div className="col-12">
          <div className={styles.panel}>
            <div className="position-relative">
              <i>
                <img
                  className={styles.closeIcon}
                  src={closeIcon}
                  alt=""
                  onClick={callbackCloseDetails}
                  onKeyDown={callbackCloseDetails}
                  role="presentation"
                />
              </i>
            </div>
            <div className={styles.programDetails}>
              <div className={`${styles.titleDiv} d-flex`}>
                <div className={styles.test}>
                  {!overrideValues.title
                    ? program.titleName.split(overrideSeparator)[0]
                    : program.titleName.split(overrideSeparator)[1]}
                </div>
                <div>
                  <img
                    class={styles.titleAttributesOverrideIcon}
                    src={
                      overrideValues.title
                        ? attributesOverrideGreenIcon
                        : attributesOverrideGreenToggledIcon
                    }
                    onClick={() => handleToggleOverrideValues('title')}
                    onKeyDown={() => handleToggleOverrideValues('title')}
                    alt=""
                    title={
                      overrideValues.title
                        ? program.titleName.split(overrideSeparator)[0]
                        : program.titleName.split(overrideSeparator)[1]
                    }
                    hidden={
                      program.titleName.split(overrideSeparator)[0].trim === '' ||
                      !program.titleName.split(overrideSeparator)[1]
                    }
                  />
                </div>
              </div>
            </div>

            <div className={styles.content}>
              <div className="row">
                <div className="col-3">
                  <div className="col-12">
                    <b className={styles.labelField}>Title ID:</b>
                    <b className={styles.fontBold}>{program.titleId}</b>
                  </div>
                  <div className="col-12">
                    {' '}
                    <b className={styles.labelField}>Deal Code:</b>
                    <b className={styles.fontBold}>
                      {' '}
                      {program.titleAttributes.DealCode}
                    </b>
                  </div>
                  <div className={`${styles.dealNameDiv} col-12`}>
                    {' '}
                    <b className={styles.labelField}>Deal Name:</b>
                    <b className={styles.fontBold}>
                      {' '}
                      {program.titleAttributes.DealName}
                    </b>
                  </div>
                </div>

                <div className="col-3">
                  <div className="col-12">
                    {' '}
                    <b className={styles.labelField}>Genre:</b>
                    <div className="d-flex">
                      <div className={styles.fontBold}>
                        {' '}
                        {!program.titleAttributes.Genre
                          ? ''
                          : !overrideValues.genre
                          ? program.titleAttributes.Genre.split(
                              overrideSeparator
                            )[0] || <br />
                          : program.titleAttributes.Genre.split(
                              overrideSeparator
                            )[1] || <br />}
                      </div>
                      <div className={styles.attributesLabel}>
                        <img
                          class={styles.attributesOverrideIcon}
                          src={
                            overrideValues.genre
                              ? attributesOverrideGreenIcon
                              : attributesOverrideGreenToggledIcon
                          }
                          onClick={() => handleToggleOverrideValues('genre')}
                          onKeyDown={() => handleToggleOverrideValues('genre')}
                          alt=""
                          title={
                            !program.titleAttributes.Genre
                              ? ''
                              : overrideValues.genre
                              ? program.titleAttributes.Genre.split(
                                  overrideSeparator
                                )[0]
                              : program.titleAttributes.Genre.split(
                                  overrideSeparator
                                )[1]
                          }
                          hidden={
                            !program.titleAttributes.Genre ||
                            program.titleAttributes.Genre.split(
                              overrideSeparator
                            )[0].trim === '' ||
                            !program.titleAttributes.Genre.split(
                              overrideSeparator
                            )[1]
                          }
                        />
                      </div>
                    </div>
                  </div>
                  <div className="col-12">
                    <b className={styles.labelField}>Duration (hh:mm:ss):</b>{' '}
                    <div className="d-flex">
                      <div className={styles.fontBold}>
                        {' '}
                        {!program.titleAttributes.Duration
                          ? ''
                          : !overrideValues.duration
                          ? program.titleAttributes.Duration.split(
                              overrideSeparator
                            )[0] || <br />
                          : program.titleAttributes.Duration.split(
                              overrideSeparator
                            )[1] || <br />}
                      </div>
                      <div className={styles.attributesLabel}>
                        <img
                          class={styles.attributesOverrideIcon}
                          src={
                            overrideValues.duration
                              ? attributesOverrideGreenIcon
                              : attributesOverrideGreenToggledIcon
                          }
                          onClick={() => handleToggleOverrideValues('duration')}
                          onKeyDown={() =>
                            handleToggleOverrideValues('duration')
                          }
                          alt=""
                          title={
                            !program.titleAttributes.Duration
                              ? ''
                              : overrideValues.duration
                              ? program.titleAttributes.Duration.split(
                                  overrideSeparator
                                )[0]
                              : program.titleAttributes.Duration.split(
                                  overrideSeparator
                                )[1]
                          }
                          hidden={
                            !program.titleAttributes.Duration ||
                            program.titleAttributes.Duration.split(
                              overrideSeparator
                            )[0].trim === '' ||
                            !program.titleAttributes.Duration.split(
                              overrideSeparator
                            )[1]
                          }
                        />
                      </div>
                    </div>
                  </div>
                  <div className="col-12">
                    <b className={styles.labelField}>Year of Production: </b>

                    <div className="d-flex">
                      <div className={styles.fontBold}>
                        {' '}
                        {!overrideValues.yearofProduction
                          ? program.titleAttributes.YearofProduction
                            ? program.titleAttributes.YearofProduction.split(
                                overrideSeparator
                              )[0] || <br />
                            : ''
                          : program.titleAttributes.YearofProduction
                          ? program.titleAttributes.YearofProduction.split(
                              overrideSeparator
                            )[1] || <br />
                          : ''}
                      </div>
                      <div className={styles.attributesLabel}>
                        <img
                          class={styles.attributesOverrideIcon}
                          src={
                            overrideValues.yearofProduction
                              ? attributesOverrideGreenIcon
                              : attributesOverrideGreenToggledIcon
                          }
                          onClick={() =>
                            handleToggleOverrideValues('yearofProduction')
                          }
                          onKeyDown={() =>
                            handleToggleOverrideValues('yearofProduction')
                          }
                          alt=""
                          title={
                            overrideValues.yearofProduction
                              ? program.titleAttributes.YearofProduction
                                ? program.titleAttributes.YearofProduction.split(
                                    overrideSeparator
                                  )[0]
                                : ''
                              : program.titleAttributes.YearofProduction
                              ? program.titleAttributes.YearofProduction.split(
                                  overrideSeparator
                                )[1]
                              : ''
                          }
                          hidden={
                            !program.titleAttributes.YearofProduction ||
                            program.titleAttributes.YearofProduction.split(
                              overrideSeparator
                            )[0].trim === '' ||
                            !program.titleAttributes.YearofProduction.split(
                              overrideSeparator
                            )[1]
                          }
                        />
                      </div>
                    </div>
                  </div>
                </div>

                <div className="col-3">
                  <div className="col-12">
                    {' '}
                    <b className={styles.labelField}>Grade:</b>{' '}
                    <div className="d-flex">
                      <div className={styles.fontBold}>
                        {' '}
                        {!program.titleAttributes.Grade
                          ? ''
                          : !overrideValues.grade
                          ? program.titleAttributes.Grade.split(
                              overrideSeparator
                            )[0] || <br />
                          : program.titleAttributes.Grade.split(
                              overrideSeparator
                            )[1] || <br />}
                      </div>
                      <div className={styles.attributesLabel}>
                        <img
                          class={styles.attributesOverrideIcon}
                          src={
                            overrideValues.grade
                              ? attributesOverrideGreenIcon
                              : attributesOverrideGreenToggledIcon
                          }
                          onClick={() => handleToggleOverrideValues('grade')}
                          onKeyDown={() => handleToggleOverrideValues('grade')}
                          alt=""
                          title={
                            !program.titleAttributes.Grade
                              ? ''
                              : overrideValues.grade
                              ? program.titleAttributes.Grade.split(
                                  overrideSeparator
                                )[0]
                              : program.titleAttributes.Grade.split(
                                  overrideSeparator
                                )[1]
                          }
                          hidden={
                            !program.titleAttributes.Grade ||
                            program.titleAttributes.Grade.split(
                              overrideSeparator
                            )[0].trim === ''||
                            !program.titleAttributes.Grade.split(
                              overrideSeparator
                            )[1]
                          }
                        />
                      </div>
                    </div>
                  </div>
                  <div className="col-12">
                    <b className={styles.labelField}>Certificate: </b>

                    <div className="d-flex">
                      <div className={styles.fontBold}>
                        {' '}
                        {program.titleAttributes.certificate === ''
                          ? ''
                          : !overrideValues.certificate
                          ? program.titleAttributes.Certificate.split(
                              overrideSeparator
                            )[0] || <br />
                          : program.titleAttributes.Certificate.split(
                              overrideSeparator
                            )[1] || <br />}
                      </div>
                      <div className={styles.attributesLabel}>
                        <img
                          class={styles.attributesOverrideIcon}
                          src={
                            overrideValues.certificate
                              ? attributesOverrideGreenIcon
                              : attributesOverrideGreenToggledIcon
                          }
                          onClick={() =>
                            handleToggleOverrideValues('certificate')
                          }
                          onKeyDown={() =>
                            handleToggleOverrideValues('certificate')
                          }
                          alt=""
                          title={
                            !program.titleAttributes.Certificate
                              ? ''
                              : overrideValues.certificate
                              ? program.titleAttributes.Certificate.split(
                                  overrideSeparator
                                )[0]
                              : program.titleAttributes.Certificate.split(
                                  overrideSeparator
                                )[1]
                          }
                          hidden={
                            program.titleAttributes.certificate === '' ||
                            program.titleAttributes.Certificate.split(
                              overrideSeparator
                            )[0].trim === '' ||
                            !program.titleAttributes.Certificate.split(
                              overrideSeparator
                            )[1]
                          }
                        />
                      </div>
                    </div>
                  </div>
                  {(component === 'Prioritisation' || component === 'Schedule') && (
                    <>
                      <div className="col-12">
                        <b className={styles.labelField}>Window Start Date</b>

                        <b className={styles.fontBold}>
                          {program.titleAttributes.WindowStartDate}
                        </b>
                      </div>
                      <div className="col-12">
                        <b className={styles.labelField}>Window End Date</b>

                        <b className={styles.fontBold}>
                          {program.titleAttributes.WindowEndDate}
                        </b>
                      </div>
                    </>
                  )}
                </div>

                <div className="col-3">
                  <div className="col-12">
                    <b className={styles.labelField}>Day Type: </b>

                    <b className={styles.fontBold}>
                      {program.titleAttributes.DayType}
                    </b>
                  </div>
                  <div className="col-12">
                    <b className={styles.labelField}>Day Period:</b>{' '}
                    <b className={styles.fontBold}>
                      {program.titleAttributes.DayPeriod}
                    </b>
                  </div>
                  <div className="col-12">
                    {' '}
                    <b className={styles.labelField}>Exhib Plays:</b>{' '}
                    <b className={styles.fontBold}>
                      {program.titleAttributes.ExhibPlays}
                    </b>
                  </div>
                </div>
              </div>

              {'Title Group/Title Exclusions'.includes(component) && (
                <>
                  <div className="row">
                    <div className="col-3">
                      <b className={styles.labelField}>Channels</b>
                    </div>
                    <div className="col-3">
                      <div className="row">
                        <div className="col-6">
                          <b className={styles.labelField}>Window Start Date</b>
                        </div>
                        <div className="col-6">
                          <b className={styles.labelField}>Window End Date</b>
                        </div>
                      </div>
                    </div>
                  </div>

                  {program.channels.map((channel) => (
                    <div className="row">
                      <div className="col-3">
                        <b className={styles.fontBold}>{channel.name}</b>
                      </div>
                      <div className="col-3">
                        <div className="row">
                          <div className="col-6">
                            <b className={styles.fontBold}>
                              {channel.windowsStartDate}
                            </b>
                          </div>
                          <div className="col-6">
                            <b className={styles.fontBold}>
                              {channel.windowsEndDate}
                            </b>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}
                </>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
ProgramDetailsComponent.propTypes = {
  program: PropTypes.shape.isRequired,
  callbackCloseDetails: PropTypes.func.isRequired,
};
export default ProgramDetailsComponent;
