import React, { useState, useRef, useEffect } from 'react';
import styles from './ImportSlots.module.css';
import ScheduleService from '../../../ScheduleService';
import DateService from '../../../../../core/date.service';
import AuthService from '../../../../../core/auth.service';
import ImportFileModalComponent from './ImportFileModalComponent';
import ImportLoadingModalComponent from './ImportLoadingModalComponent';
import ImportSuccessModalComponent from './ImportSuccessModalComponent';
import ImportConfirmModalComponent from './ImportConfirmModalComponent';

export function ImportSlotsComponent({
  closeModal,
  setScheduleData,
  scheduledata,
  clockStart,
  scheduleInfo,
}) {
  const [isLoading, setIsLoading] = useState(false);
  const [selectedFile, setSelectedFile] = useState();
  const [headerData, setHeaderData] = useState({});
  const [errorMessage, setErrorMessage] = useState('');
  const [uploadSuccess, setUploadSuccess] = useState(false);
  const [uploadedList, setUploadedList] = useState([]);
  const [confirmModal, setConfirmModal] = useState(false);
  const [uploadedSlots, setUploadedSlots] = useState([]);

  const loadingHeaderComponent = (
    <div className={styles.divHeader}>
      Importing and validating slots from{' '}
      <b>{selectedFile && selectedFile.name}</b>
    </div>
  );

  const uploadFile = (file, fileExtension, overwrite) => {
    const slotData = scheduledata.map((dataItem, index) => ({
      id: index,
      start: dataItem.start,
      end: dataItem.end,
      startDate: new Date(
        Date.UTC(
          new Date(dataItem.startDate).getFullYear(),
          new Date(dataItem.startDate).getMonth(),
          new Date(dataItem.startDate).getDate(),
          new Date(dataItem.startDate).getHours(),
          new Date(dataItem.startDate).getMinutes()
        )
      ),
      endDate: new Date(
        Date.UTC(
          new Date(dataItem.endDate).getFullYear(),
          new Date(dataItem.endDate).getMonth(),
          new Date(dataItem.endDate).getDate(),
          new Date(dataItem.endDate).getHours(),
          new Date(dataItem.endDate).getMinutes()
        )
      ),
      title: dataItem.title,
      description: 'description',
      isAllDay: false,
      duration: dataItem.duration,
      durationCurrent: dataItem.duration,
      fixed: dataItem.fixed,
    }));

    const formData = new FormData();
    formData.append('file', file);
    formData.append('schedule', JSON.stringify({ slots: slotData }));
    setIsLoading(true);

    ScheduleService.uploadScheduleFile(
      formData,
      fileExtension,
      headerData.period,
      headerData.filter,
      overwrite,
      scheduledata.length
    )
      .then((response) => {
        const responseData = response.data.scheduleSlots.map(
          (dataItem, index) => ({
            id: index,
            start: new Date(DateService.addHours(dataItem.start, clockStart)),
            end: new Date(DateService.addHours(dataItem.end, clockStart)),
            startDate: new Date(dataItem.start),
            endDate: new Date(dataItem.end),
            title: dataItem.title,
            description: 'description',
            isAllDay: false,
            duration: dataItem.duration,
            durationCurrent: dataItem.duration,
            fixed: dataItem.fixed,
          })
        );
        if (response.data.scheduleSlots.length > 0) {
          setScheduleData(responseData);
          setUploadedSlots(responseData);
        }
        setUploadedList(response.data.items);
        setUploadSuccess(true);
      })
      .catch((error) => {
        setErrorMessage(error.response.data.message);
        setConfirmModal(false);
      })
      .finally(() => {
        setIsLoading(false);
      });
  };

  const handleOnImportFile = () => {
    const acceptableFiles = ['xlsx', 'xls', 'csv'];
    const fileExtension = selectedFile.name.split('.').pop();

    if (acceptableFiles.includes(fileExtension)) {
      if (scheduledata.length > 0) setConfirmModal(true);
      else uploadFile(selectedFile, fileExtension, false);
    } else {
      setErrorMessage(
        'Uploaded file type is not supported. Please use files in the following format (xlsx, csv)'
      );
    }
  };

  const handleOverwrite = () => {
    const fileExtension = selectedFile.name.split('.').pop();
    uploadFile(selectedFile, fileExtension, true);
  };

  const handleCreate = () => {
    const fileExtension = selectedFile.name.split('.').pop();
    uploadFile(selectedFile, fileExtension, false);
  };

  useEffect(() => {
    const data = AuthService.getSelectedSchedule();
    var periodSplit = data.data.period.split('-');
    var startSplit = periodSplit[0].split('/');
    var endSplit = periodSplit[1].split('/');
    var startDate = new Date(
      Date.UTC(
        parseInt(startSplit[2]),
        parseInt(startSplit[1]) - 1,
        parseInt(startSplit[0]),
        6,
        0
      )
    );
    var endDate = new Date(
      Date.UTC(
        parseInt(endSplit[2]),
        parseInt(endSplit[1]) - 1,
        parseInt(endSplit[0]),
        23,
        59
      )
    );
    var header = {
      filter: data.filter,
      period: { start: startDate.toUTCString(), end: endDate.toUTCString() },
    };
    setHeaderData(header);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return isLoading ? (
    <ImportLoadingModalComponent headerComponent={loadingHeaderComponent} />
  ) : uploadSuccess ? (
    <ImportSuccessModalComponent
      uploadedList={uploadedList}
      closeModal={closeModal}
      scheduleInfo={scheduleInfo}
    />
  ) : confirmModal ? (
    <ImportConfirmModalComponent
      closeModal={closeModal}
      handleOverwrite={handleOverwrite}
      handleCreate={handleCreate}
    />
  ) : (
    <ImportFileModalComponent
      closeModal={closeModal}
      selectedFile={selectedFile}
      setSelectedFile={(e) => {
        setSelectedFile(e);
        setErrorMessage('');
      }}
      handleOnImportFile={handleOnImportFile}
      errorMessage={errorMessage}
      setErrorMessage={(e) => setErrorMessage(e)}
    />
  );
}
